<template lang="html">
  <div class="bby-box-component">
    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>

    <transition
      name="expand"
      @enter="enter"
      @after-enter="afterEnter"
      @leave="leave"
    >
      <div class="bby-range-slider-error bby-error" v-show="showError">
        <p>This value cannot be empty.</p>
      </div>
    </transition>

    <div class="bby-component bby-text" v-for="(firstComponentProp, index) in firstComponentProps.items" :key="index" :class="{inputError: showError}">
      <textarea
        class="bby-input-text  bby-bg-primaryReverseWhite bby-stroke-primaryMediumGray"
        :cols="firstComponentProp.columns"
        :rows="firstComponentProp.rows"
        :name="firstComponentProps.name"
        :value="getInputTextArea()"
        @input="updateInputTextArea"
        :placeholder="setPlaceHolder(firstComponentProps.name)"
         />
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data () {
    return {
      inputText: '',
      showError: false
    }
  },
  props: {
    firstComponentProps: Object,
    title: String
  },
  computed:{
    ...mapState({
      textAreaState (state){
        return state.values[this.firstComponentProps.name]
      }
    }),
  },
  beforeMount(){
    if (this.$store.state.values[this.firstComponentProps.name] == null) {
      this.$store.commit("UPDATE_COMPONENT_VALUES", [{name: this.firstComponentProps.name, value: this.firstComponentProps.selected_value}])
    }
    this.inputText = this.firstComponentProps.selected_value
  },
  watch:{
    showError(val){
      this.$store.commit("UPDATE_COMPONENT_ERRORS", [{value: val, name: this.firstComponentProps.name}]);
    }
  },
  methods:{
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
       el.addEventListener('scroll', this.handleScroll)

    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    },
    updateInputTextArea(event){
      if (event.target.value){
        this.showError = false
        // event.target.value = event.target.value.replace(/\"/g, "\\\"")
        event.target.value = event.target.value
        this.$store.commit("UPDATE_COMPONENT_VALUES", [{value: event.target.value, name: event.target.name}]);
      } else {
        this.showError = true
      }
      this.inputText = event.target.value;
    },
    getInputTextArea(componentName){
      if (this.inputText==''){
        this.showError = true
        return this.inputText
      }
      // return this.inputText.replace(/\\\"/g, "\"")
      return this.inputText
    },
    setPlaceHolder(componentName){
      if (!this.$store.state.values[componentName] && this.inputText=='' && this.firstComponentProps.selected_value==''){
        return this.firstComponentProps.items[0].value
      }
    }

  },
  beforeDestroy(){
    this.$store.commit("UPDATE_COMPONENT_ERRORS", [{value: false, name: this.firstComponentProps.name}]);
  }
}
</script>

