<template lang="html">
  <div class="bby-box-component">

    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>

    <transition
      name="expand"
      @enter="enter"
      @after-enter="afterEnter"
      @leave="leave"
    >
      <div class="bby-range-slider-error bby-error" v-show="showError">
        <p>This value cannot be empty.</p>
      </div>
    </transition>

    <div class="bby-component bby-text" v-for="(firstComponentProp, index) in firstComponentProps.items" :key="index" :class="{inputError: showError}">
      <input
        class="bby-input-text  bby-bg-primaryReverseWhite bby-stroke-primaryMediumGray"
        type="text"
        :value="inputText"
        :name="firstComponentProps.name"
        @input="updateInputText"
        autocomplete="off"
      />
    </div>


  </div>
</template>

<script>
import { mapState } from 'vuex';
import _ from 'lodash';

export default {
  data () {
    return {
      inputText: '',
      showError: false
    }
  },
  props: {
    firstComponentProps: Object,
    title: String
  },
  beforeMount(){
    if (this.$store.state.values[this.firstComponentProps.name] == null) {
      this.$store.commit("UPDATE_COMPONENT_VALUES", [{name: this.firstComponentProps.name, value: this.firstComponentProps.selected_value}])
    }
    else {
      this.inputText = this.decodeHtmlChars(this.inputTextState)
    }
  },
  computed:{
    ...mapState({
      inputTextState (state){
        return state.values[this.firstComponentProps.name]
      }
    })
  },
  watch:{
    inputTextState(val){
      this.inputText = this.decodeHtmlChars(val)
    },
    showError(val){
      this.$store.commit("UPDATE_COMPONENT_ERRORS", [{value: val, name: this.firstComponentProps.name}]);
    }
  },
  methods:{
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
       el.addEventListener('scroll', this.handleScroll)

    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    },
    encodeHtmlChars(str) {
    var i = str.length,
        result = [];

    while (i--) {
        var charCode = str[i].charCodeAt();
        // if (charCode < 65 || charCode > 127 || (charCode>90 && charCode<97)) {
        if (charCode < 48 || (charCode > 57 && charCode < 65) || (charCode > 90 && charCode < 97) || charCode > 123) {
            result[i] = '&#'+charCode+';';
        } else {
            result[i] = str[i];
        }
    }
    return result.join('');
    },
    decodeHtmlChars(str) {
      return str.replace(/(&#(\d+);)/g, function(match, capture, charCode) {
        return String.fromCharCode(charCode);
      });
    },
    updateInputText:
      _.debounce(function(event){
        let encodedString = '';
        if (event.target.value){
          this.showError = false
        } else {
          this.showError = true
        }
        encodedString = this.encodeHtmlChars(event.target.value)
        this.$store.commit("UPDATE_COMPONENT_VALUES", [{value: encodedString, name: event.target.name}]);
      }, 50),
    }
}
</script>

