<template lang="html">
  <div class="bby-component bby-functional-component">
    <div v-for="(firstComponentProp, index) in firstComponentProps.items"  :key="index" class="bby-box bby-bg-primaryReverseWhite" :class="error(firstComponentProp.component.name)">
        <component
          :is="firstComponentProp.component.type"
          :firstComponentProps="firstComponentProp.component"
          :title="firstComponentProp.propertyName"
        ></component>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import _ from 'lodash';

export default {
  data () {
    return {
      svgText: '',
      parsedFontObject: {}
    }
  },
  props: {
    firstComponentProps: Object,
  },
  beforeMount(){
    if (this.$store.state.values[this.firstComponentProps.name] == null) {
      this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: this.firstComponentProps.selected_value}])
    }
  },
  computed:{
    ...mapState({
      logoInputTextState: state => state.values.textInputText,
      logoTextSizeState: state => state.values.textFontSizeRangeSlider,
      logoTextFontFamilyState: state => state.values.textFontFamilyDropDownList,
      logoTextWeightState: state => state.values.textFontWeightDropDownList,
      logoTextLetterSpacingState: state => state.values.textFontLetterSpacingRangeSlider,
      logoTextColorState: state => state.values.textColorColorPicker,
      logoTextTopOffsetState: state => state.values.textTopOffsetRangeSlider
    })
  },
  methods:{
    error(value){
      if (this.$store.state.componentErrors[value]) {
        return 'bby-box-error'
      }
    },
    parseFontURLToObject(fontURL){
      const opentype = require('opentype.js');
      var that = this
      opentype.load(fontURL, function(err, font) {
    if (err) {
        alert('Font could not be loaded: ' + err);
    } else {
      that.parsedFontObject = font
      that.generateLogoTextSVG()
            }
        });
        },
    decodeHtmlChars(str) {
        return str.replace(/(&#(\d+);)/g, function(match, capture, charCode) {
            return String.fromCharCode(charCode);
      });
    },
    generateLogoTextSVG(){
          var computeLayout = require('opentype-layout-improved');
          if (Object.keys(this.parsedFontObject).length){
            var svgSymbolsPaths = []
            var logoText = this.decodeHtmlChars(this.logoInputTextState)
            for (let char in logoText) {
              var glyph = this.parsedFontObject.charToGlyph(logoText[char])
              var path = glyph.getPath(0, parseInt(this.logoTextSizeState), parseInt(this.logoTextSizeState))
              var glyphCoords = path.getBoundingBox()
              var glyphWidth = Math.floor(this.parsedFontObject.getAdvanceWidth(logoText[char], parseInt(this.logoTextSizeState)))
              var glyphSVG = path.toSVG()
              svgSymbolsPaths.push({
                glyphWidth: glyphWidth,
                glyphSVG: glyphSVG
              })
            }
            var textInSVG = ""
            var currentPositionX = 0
            for (let symbol in svgSymbolsPaths){
              textInSVG = `${textInSVG}<svg x="${currentPositionX}" y="0">${svgSymbolsPaths[symbol].glyphSVG}</svg>`
              currentPositionX = currentPositionX + svgSymbolsPaths[symbol].glyphWidth + parseInt(this.logoTextLetterSpacingState)
            }

            this.svgText = textInSVG
        }
        }
  },
  watch:{
    svgText(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        // newval = newval.replace(/\"/g, "\\\"")
        newval = newval
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {logoTextSVG: newval}}])
      }
    },
    logoInputTextState:
    _.throttle(function(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {logoText: newval}}])
        this.generateLogoTextSVG()
      }
    }, 150),
    logoTextSizeState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {logoFontSize: newval}}])
        this.generateLogoTextSVG()

      }
    },
    logoTextFontFamilyState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {logoFontFamily: newval}}])
        this.generateLogoTextSVG()
      }
    },
    logoTextWeightState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: newval}])
      }
    },
    'logoTextWeightState.logoFontUrl': function (newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.parseFontURLToObject(newval)
      }
    },

    logoTextLetterSpacingState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {logoFontLetterSpacing: newval}}])
        this.generateLogoTextSVG()
      }
    },
    logoTextColorState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {logoFontColor: newval}}])
      }
    },
    logoTextTopOffsetState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {logoTextTopOffset: newval}}])
      }
    }
  }
}
</script>

