<template lang="html">
    <div class="bby-box-component" @scroll="handleScroll" id="previewComponent">

      <div class="bby-logo-preview" id="getPreviewHeight" v-if="Object.entries(logoIconValues).length">
        <div class="bby-logo-preview-wrapper" id="previewContent">

          <div class="bby-logo-preview-inner">
            <span class="bby-preview-inner-title">logo preview</span>
            <div class="logo-preview" id="previewSvg">

              <div class="bby-bg-color-prev bby-style-flex bby-column bby-center bby-start">
                <button class="bby-icon-button bby-no-bg bby-bg-color-prev-open" @click="bgSaturationToggle('bby-color-list-ul-svg')"><BaseIconBby name="bbyColorBucket" width="15" height="15" fill="#ccc" /></button>
                <ul id="bby-color-list-ul-svg" class="bby-color-list-ul">
                  <li v-for="(prevBgColor, index) in prevBgColors" :key="index">
                    <button class="bby-icon-button bby-no-bg" @click="bgSaturation(prevBgColor, 'previewSvg')"><span class="bby-color-list" :style="'background:'+ prevBgColor"></span></button>
                  </li>
                </ul>
              </div>

              <div class="logo-preview-overflow">

                <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="176" height="40" id="svgForPreview" class="svgPreview" x="0">
                  <svg class="logoIcon" :height="logoIconValues.iconSize" :width="logoIconValues.iconSize" x="0" :y="logoIconOffsetY" :dy="logoIconOffsetY">
                    <svg viewBox="0 0 512 512" :fill="logoIconValues.iconColor.iconColorColorPicker_hex" v-html="symbolSvg">
                    <rect width="100%" height="100%" fill-opacity="0"/>
                    </svg>
                  </svg>
                    <svg class="logoText" :fill="logoTextValues.logoFontColor.textColorColorPicker_hex" v-html="logoTextSVG">
                    </svg>

                </svg>
              </div>
              <button class="bby-button bby-button-small  bby-bg-primaryIndigo" @click="pngGenerate">save to media library</button>
            </div>
          </div>

          <transition
          name="expand"
          @enter="enter"
          @after-enter="afterEnter"
          @leave="leave"
          >

          <div v-if="showPngLogo" class="bby-logo-buttons" id="bbyLogoButtons">

            <div class="bby-bg-color-prev">
              <button class="bby-icon-button bby-no-bg bby-bg-color-prev-open" @click="bgSaturationToggle('bby-color-list-ul-png')"><BaseIconBby name="bbyColorBucket" width="15" height="15" fill="#ccc" /></button>
              <ul id="bby-color-list-ul-png" class="bby-color-list-ul">
                <li v-for="(prevBgColor, index) in prevBgColors" :key="index">
                  <button class="bby-icon-button bby-no-bg" @click="bgSaturation(prevBgColor, 'bbyImgReadyChangeBg')"><span class="bby-color-list" :style="'background:'+ prevBgColor"></span></button>
                </li>
              </ul>
            </div>


            <div class="bby-logo-buttons-close">
              <button type="button" @click="closeLogoButtons"><BaseIconBby name="bbyDel" width="20" height="20" /></button>
            </div>
            <div class="bby-logo-buttons-png-image">
              <span class="bby-preview-png-title">your logo is ready to use</span>
              <img class="logo-generated" id="bbyImgReadyChangeBg" :src="logoUrl">
            </div>
            <ul class="bby-logo-buttons-png-download">
              <li><a :href="logoUrl" title="Download PNG" download><BaseIconBby name="bbyPng" width="25" height="25" /></a></li>
              <li>
                <button @click="downloadSVG" title="Download SVG"><BaseIconBby name="bbySvg" width="25" height="25" /></button>
                <input type="hidden"/>
              </li>
              <li>
                <button @click="copyToClipboard" title="Copy URL to clipboard"><BaseIconBby name="bbyCopyUrl" width="25" height="25" /></button>
                <input type="hidden" id="ctc" :value="logoUrl" />
              </li>
            </ul>
            <div class="bby-logo-buttons-break">
              or
            </div>
            <div class="bby-logo-buttons-png-download">
              <a class="bby-button-stroke bby-button-medium bby-stroke-primaryGray" href="/wp-admin/upload.php">go to media library</a>
            </div>
            <b v-if="loading"><img src="https://www.bluechipexterminating.com/wp-content/uploads/2020/02/loading-gif-png-5.gif" width="25px" alt=""></b>
          </div>
        </transition>

        </div>
      </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
import TabsService from "@/services/TabsService.js";
import axios from "axios";
import sendNotification from "@/services/NotificationService.js";

export default {
  data () {
    return {
      logoPNG:'',
      absoluteTop:0,
      childHeight: 0,
      fBottom: false,
      parentBottom: 0,
      childTop: 0,
      logoIconValues: {},
      logoTextValues: {},
      textFontBase64: '',
      logoTextSVG: '',
      iconSizeX: 0,
      iconPosition: '',
      logoTextOffsetY: 0,
      logoIconOffsetY: 0,
      showIcon: false,
      loading: false,
      showPngLogo: false,
      fixedClass: '',
      symbolSvg: '',
      prevBgColors:['#ffffff', '#000000', '#02f7f8'],
      showBgSat: false
    }
  },
  props:{
    logoUrl: String
  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
    this.logoTextValues = this.$store.state.values.logoFunctionalLogoText
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods:{
    bgSaturationToggle(id){
      this.showBgSat = !this.showBgSat
      var changeWidth = document.getElementById(id)
      changeWidth.classList.toggle('animate')
    },
    bgSaturation(color, id){
      var bgToChange = document.getElementById(id)
      bgToChange.style.background = color
    },
    copyToClipboard(){
      var ctc = document.getElementById("ctc");
      ctc.removeAttribute("type","hidden");
      ctc.select();
      ctc.setSelectionRange(0, 99999);
      document.execCommand("copy");
      ctc.setAttribute("type","hidden");
      sendNotification("Logo URL copied to clipboard", "success")
    },
    downloadSVG(){
      var logoSVG = document.getElementById("svgForPreview")
      var logoSVGClone = logoSVG.cloneNode(true);
      logoSVGClone.setAttribute('preserveAspectRatio', 'xMidYMid meet')
      var viewBoxWidth = logoSVGClone.getAttribute("width")
      var viewBoxHeight = logoSVGClone.getAttribute("height")
      logoSVGClone.setAttribute('viewBox', `0 0 ${viewBoxWidth} ${viewBoxHeight}`)
      logoSVGClone.removeAttribute('id')
      logoSVGClone.removeAttribute('width')
      logoSVGClone.removeAttribute('height')
      logoSVGClone.removeAttribute('class')

      logoSVGClone = logoSVGClone.outerHTML
      let filename = this.logoUrl.substring(this.logoUrl.lastIndexOf('/')+1)
      filename = filename.replace('.png', '.svg')

      var downloadElement = document.createElement('a');
      downloadElement.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(logoSVGClone));
      downloadElement.setAttribute('download', filename)
      downloadElement.style.display = 'none'
      document.body.appendChild(downloadElement)
      downloadElement.click()
      document.body.removeChild(downloadElement)
    },
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    },
    closeLogoButtons(){
      this.showPngLogo = false;
    },
    pngGenerate(){
      const diffDevice = window.matchMedia("(min-width: 599px)");
      var scroll = document.getElementById("getPreviewHeight");
      var previewColLeft = document.getElementById("previewColLeft").getBoundingClientRect();
      for (let i in this.componentErrorsState){
        this.formError = this.componentErrorsState[i]
        if (this.formError) break
      }
      if (this.formError) {
        sendNotification('Ooops some fields are incorrect!', 'errors')
      } else {
        this.$store.commit('replaceValues')
        this.$emit('generate')
        this.loading = true;
        this.showPngLogo = false;
        if (!diffDevice.matches) {
          window.scrollTo({behavior: "smooth", top: previewColLeft.height});
        }
        if (this.logoUrl != 0) {
          setTimeout(() => {
            this.loading = false;
            this.showPngLogo = true;
          }, 2000)
        }
      }
    },
    recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText){
          var svg = document.getElementById('svgForPreview');
          var bboxLogoIcon = LogoIcon.getBBox();
          var bboxLogoText = LogoText.getBBox();
          var svgSizeY
          var svgSizeX
          var textLeftOffset
          const paddingIconWidth = 5

          this.iconSizeX = bboxLogoIcon.width + paddingIconWidth;
          if (bboxLogoText.height + bboxLogoText.y >= bboxLogoIcon.height + bboxLogoIcon.y){
            svgSizeY = bboxLogoText.height + bboxLogoText.y
          } else if (bboxLogoText.height + bboxLogoText.y < bboxLogoIcon.height + bboxLogoIcon.y ) {
            svgSizeY = this.logoIconValues.iconSize
          }

          var svgSizeWithOffset = Math.ceil(svgSizeY)
          if (this.showIcon){
            svgSizeWithOffset = svgSizeWithOffset + Math.abs(parseInt(this.logoTextOffsetY, 10)) + Math.abs(parseInt(this.logoIconOffsetY, 10))
            textLeftOffset = this.iconSizeX + parseInt(this.logoIconValues.iconLeftOffset)
            let textYPosition = parseInt(this.logoTextValues.logoTextTopOffset)
            LogoText.setAttributeNS(null, "x", textLeftOffset);
            LogoIcon.setAttributeNS(null, "transform","translate(0)");
            LogoIcon.setAttributeNS(null, "x",0);
            LogoText.setAttributeNS(null, "y", textYPosition);
            svgSizeX = Math.ceil(bboxLogoIcon.width + bboxLogoText.width + paddingIconWidth + 3 + parseInt(this.logoIconValues.iconLeftOffset))
            svgSizeY = Math.ceil(svgSizeY)
          } else {
            svgSizeWithOffset = svgSizeWithOffset + Math.abs(parseInt(this.logoTextOffsetY, 10))
            let textYPosition = parseInt(this.logoTextValues.logoTextTopOffset)
            LogoText.setAttributeNS(null, "y", textYPosition);
            LogoText.setAttributeNS(null, "x","0");
            LogoIcon.setAttributeNS(null, "transform","translate(-" + this.iconSizeX +")");
            LogoIcon.setAttributeNS(null, "x",-parseInt(this.iconSizeX));
            svgSizeX = Math.ceil(bboxLogoText.width + 3)
            svgSizeY = Math.ceil(svgSizeY)

          }

          svg.setAttribute("width", svgSizeX)
          svg.setAttribute("height", svgSizeWithOffset)

          var getPreviewHeightRect = getPreviewHeight.getBoundingClientRect();
          var previewSvg = document.getElementById("previewSvg");
          var previewContent = document.getElementById("previewContent");
          var previewSvgRect = previewSvg.getBoundingClientRect();

          previewContent.style.maxWidth = getPreviewHeightRect.width+"px";

          if (getPreviewHeightRect.width = previewSvgRect.width) {
            previewContent.classList.add("horizontalScroll");
          }
          else {
            previewContent.classList.remove("horizontalScroll");
          }
    },
    recalculateSVGsizeIconPositionTop(LogoIcon, LogoText){
          var svg = document.getElementById('svgForPreview');
          var bboxLogoIcon = LogoIcon.getBBox();
          var bboxLogoText = LogoText.getBBox();
          var svgSizeY
          var svgSizeX
          var textLeftOffset
          const paddingIconWidth = 5

          // calculate SVG container height
          this.iconSizeX = bboxLogoIcon.width + paddingIconWidth;
          svgSizeY = bboxLogoText.height + bboxLogoText.y + parseInt(this.logoIconValues.iconSize) + paddingIconWidth


          // calculate icon position

          let iconXPosition = (bboxLogoText.width / 2) - (parseInt(this.logoIconValues.iconSize) / 2)
          let textYPosition = parseInt(this.logoIconValues.iconSize) + parseInt(this.logoTextValues.logoTextTopOffset)
          LogoIcon.setAttributeNS(null, "x",iconXPosition);
          textLeftOffset = parseInt(this.logoIconValues.iconLeftOffset)
          LogoText.setAttributeNS(null, "x", '0');
          LogoIcon.setAttributeNS(null, "x", iconXPosition + textLeftOffset);
          LogoText.setAttributeNS(null, "y", textYPosition);
          var svgSizeWithOffset = Math.ceil(svgSizeY)
          if (this.showIcon){
            svgSizeWithOffset = svgSizeWithOffset + Math.abs(parseInt(this.logoTextOffsetY, 10)) + Math.abs(parseInt(this.logoIconOffsetY, 10))
            textLeftOffset = this.iconSizeX + parseInt(this.logoIconValues.iconLeftOffset)
            // LogoText.setAttributeNS(null, "x", textLeftOffset);
            LogoIcon.setAttributeNS(null, "transform","translate(0)");
            // LogoIcon.setAttributeNS(null, "x",0);
            svgSizeX = Math.ceil(bboxLogoText.width + paddingIconWidth + 3)
            svgSizeY = Math.ceil(svgSizeY)
          } else {
            svgSizeWithOffset = svgSizeWithOffset + Math.abs(parseInt(this.logoTextOffsetY, 10))
            LogoText.setAttributeNS(null, "y","0");
            LogoText.setAttributeNS(null, "x","0");
            LogoIcon.setAttributeNS(null, "transform","translate(-" + this.iconSizeX +")");
            LogoIcon.setAttributeNS(null, "x",-parseInt(this.iconSizeX));
            svgSizeX = Math.ceil(bboxLogoText.width + 3)
            svgSizeY = Math.ceil(svgSizeY)

          }

          svg.setAttribute("width", svgSizeX)
          svg.setAttribute("height", svgSizeWithOffset)

          var getPreviewHeightRect = getPreviewHeight.getBoundingClientRect();
          var previewSvg = document.getElementById("previewSvg");
          var previewContent = document.getElementById("previewContent");
          var previewSvgRect = previewSvg.getBoundingClientRect();

          previewContent.style.maxWidth = getPreviewHeightRect.width+"px";

          if (getPreviewHeightRect.width = previewSvgRect.width) {
            previewContent.classList.add("horizontalScroll");
          }
          else {
            previewContent.classList.remove("horizontalScroll");
          }
    },
    handleScroll() {
      const diffDevice = window.matchMedia("(min-width: 599px)");
      var previewColLeft = document.getElementById("previewColLeft").getBoundingClientRect();
      var getPreviewHeight = document.getElementById("getPreviewHeight");
      var getPreviewHeightRect = getPreviewHeight.getBoundingClientRect();
      var previewContent = document.getElementById("previewContent");
      var previewContentRect = previewContent.getBoundingClientRect();
      var viewPort = window.innerHeight;

      if (!diffDevice.matches && this.showPngLogo) {
        var bbyLogoButtons = document.getElementById("bbyLogoButtons").getBoundingClientRect();
        var inViewportMobile = viewPort - getPreviewHeightRect.bottom + bbyLogoButtons.height + 20;
      }
      else {
        var inViewportMobile = viewPort - getPreviewHeightRect.bottom;
      }

      if (inViewportMobile < 0 && !diffDevice.matches || (getPreviewHeightRect.y < 0 && diffDevice.matches && previewColLeft.bottom - (previewContentRect.height) > 0)) {
        getPreviewHeight.classList.add("fixedPrev");
        getPreviewHeight.classList.remove("nofixedPrev");
        getPreviewHeight.classList.remove("fixedBottom");
      }
      else if (previewColLeft.bottom - previewContent.getBoundingClientRect().height < 0 && diffDevice.matches) {
        getPreviewHeight.classList.add("fixedBottom");
        getPreviewHeight.classList.remove("nofixedPrev");
        getPreviewHeight.classList.remove("fixedPrev");
      }
      else {
        getPreviewHeight.classList.add("nofixedPrev");
        getPreviewHeight.classList.remove("fixedPrev");
        getPreviewHeight.classList.remove("fixedBottom");
        getPreviewHeight.style.height = previewContentRect.height+"px"
      }
    }
    },
  computed:{
    ...mapState({
      logoIconValuesState: state => state.values.logoFunctionalLogoIcon,
      logoTextValuesState: state => state.values.logoFunctionalLogoText,
      logoIconTypeState: state => state.values.iconFunctionalLogoIcons,
      componentErrorsState: state => state.componentErrors
    }),
  },
  watch: {
    logoIconValuesState(val){
      this.logoIconValues = val
    },
    logoTextValuesState(val){
      this.logoTextValues = val
    },
    'logoIconValues.iconSize': function (newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$nextTick(() => {
        var LogoIcon = document.getElementsByClassName('logoIcon')[0]
        var LogoText = document.getElementsByClassName('logoText')[0]
        if (LogoIcon && LogoText){
          if (this.iconPosition.toLowerCase() == 'left'){
            this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
          } else if (this.iconPosition.toLowerCase() == 'top'){
            this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
          }
        }
      })
    }
  },

  'logoIconValues.iconSvgCode': function (newval, oldval){
    if (!_.isEqual(newval, oldval)){

    // this.symbolSvg = newval.replace(/\\\"/g, "\"")
    this.symbolSvg = newval

    this.$nextTick(() => {
      var LogoIcon = document.getElementsByClassName('logoIcon')[0]
      var LogoText = document.getElementsByClassName('logoText')[0]
      if (LogoIcon && LogoText){
        if (this.iconPosition.toLowerCase() == 'left'){
          this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
        } else if (this.iconPosition.toLowerCase() == 'top'){
          this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
        }
      }
    })
  }
},
'logoIconValues.iconPosition': function (newval, oldval){
  if (!_.isEqual(newval, oldval)){
  this.$nextTick(() => {
    var LogoIcon = document.getElementsByClassName('logoIcon')[0]
    var LogoText = document.getElementsByClassName('logoText')[0]
    this.iconPosition = newval.toLowerCase()
    if (LogoIcon && LogoText){
      if (this.iconPosition.toLowerCase() == 'left'){
        this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
      } else if (this.iconPosition.toLowerCase() == 'top'){
        this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
      }
    }
  })
}
},
  'logoIconValues.iconUse': function (newval, oldval){
    if (!_.isEqual(newval, oldval)){
      // if (oldval == 'false') {
      //   document.getElementsByClassName('logoIcon')[0].style.visibility = 'hidden'
      // }
      if (newval == 'true') {
        this.showIcon = true
        this.$nextTick(() => {
        document.getElementsByClassName('logoIcon')[0].style.visibility = 'visible'
        var LogoIcon = document.getElementsByClassName('logoIcon')[0]
        var LogoText = document.getElementsByClassName('logoText')[0]
        if (LogoIcon && LogoText){
          if (this.iconPosition.toLowerCase() == 'left'){
            this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
          } else if (this.iconPosition.toLowerCase() == 'top'){
            this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
          }
        }
      })
      } else {
        this.showIcon = false
        var LogoIcon = document.getElementsByClassName('logoIcon')[0]
        var LogoText = document.getElementsByClassName('logoText')[0]
        this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
      }

      // this.$nextTick(() => {
      //   var LogoIcon = document.getElementsByClassName('logoIcon')[0]
      //   var LogoText = document.getElementsByClassName('logoText')[0]
      //   if (LogoIcon && LogoText){
      //     this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
      //   }
      // })
    }
  },
  logoTextValues(newval, oldval){
    if (!_.isEqual(newval, oldval)){
      this.$nextTick(() => {
        var LogoIcon = document.getElementsByClassName('logoIcon')[0]
        var LogoText = document.getElementsByClassName('logoText')[0]
        if (LogoIcon && LogoText){
          if (this.iconPosition.toLowerCase() == 'left'){
            this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
          } else if (this.iconPosition.toLowerCase() == 'top'){
            this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
          }
        }
      })
    }
  },
  'logoTextValues.logoTextTopOffset': function (newval, oldval){
    if (!_.isEqual(newval, oldval)){
      this.logoTextOffsetY = newval
      this.$nextTick(() => {
        var LogoIcon = document.getElementsByClassName('logoIcon')[0]
        var LogoText = document.getElementsByClassName('logoText')[0]
        if (LogoIcon && LogoText){
          if (this.iconPosition.toLowerCase() == 'left'){
            this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
          } else if (this.iconPosition.toLowerCase() == 'top'){
            this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
          }
        }
      })
    }
  },
  'logoTextValues.logoTextSVG': function (newval, oldval){
    if (!_.isEqual(newval, oldval)){
      // this.logoTextSVG = newval.replace(/\\\"/g, "\"")
      this.logoTextSVG = newval
      this.$nextTick(() => {
        var LogoIcon = document.getElementsByClassName('logoIcon')[0]
        var LogoText = document.getElementsByClassName('logoText')[0]
        if (LogoIcon && LogoText){
          if (this.iconPosition.toLowerCase() == 'left'){
            this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
          } else if (this.iconPosition.toLowerCase() == 'top'){
            this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
          }
        }
      })
    }
  },
  'logoIconValues.iconLeftOffset': function (newval, oldval){
    if (!_.isEqual(newval, oldval)){
      this.$nextTick(() => {
        var LogoIcon = document.getElementsByClassName('logoIcon')[0]
        var LogoText = document.getElementsByClassName('logoText')[0]
        if (LogoIcon && LogoText){
          if (this.iconPosition.toLowerCase() == 'left'){
            this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
          } else if (this.iconPosition.toLowerCase() == 'top'){
            this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
          }
        }
      })
    }
  },

  'logoIconValues.iconTopOffset': function (newval, oldval){
    if (!_.isEqual(newval, oldval)){
      if (newval){
        this.logoIconOffsetY = newval
      } else {
        this.logoIconOffsetY = '0'
      }
      this.$nextTick(() => {
        var LogoIcon = document.getElementsByClassName('logoIcon')[0]
        var LogoText = document.getElementsByClassName('logoText')[0]
        if (LogoIcon && LogoText){
          if (this.iconPosition.toLowerCase() == 'left'){
            this.recalculateSVGsizeIconPositionLeft(LogoIcon, LogoText)
          } else if (this.iconPosition.toLowerCase() == 'top'){
            this.recalculateSVGsizeIconPositionTop(LogoIcon, LogoText)
          }
        }
      })
    }
  }
}
}
</script>

<style lang="scss" scoped>

.bby-color-list{
  width: 10px;
  height: 10px;
  border: 1px solid #ccc;
  display: inline-block;
}

.bby-bg-color-prev{
  position: absolute;
  top: 0;
  left: 0;
}

.bby-logo-preview-wrapper .logo-preview{
  padding: 25px 25px 25px 25px;
}

.bby-bg-color-prev .bby-bg-color-prev-open{
  padding: 5px 2px !important;
}

.bby-logo-buttons-png-download li a, .bby-logo-buttons-png-download li button{
  color:#000;
  font-weight: 600;
  font-size: 15px;
  letter-spacing: 0;
}

.bby-logo-buttons-png-download li a:hover{
  text-decoration: none !important
}

.bby-color-list-ul{
  margin: 0 !important;
  padding: 0 !important;
  height: 0;
  max-height: 0;
  overflow: hidden;
}

.bby-color-list-ul li{
  margin: 0 !important
}

.bby-color-list-ul.animate{
  height: 100px;
  max-height: 100%
}

.bby-logo-preview-inner{
  position: relative;
}

.logoIcon.toggle{
  opacity: 0;
}

/* applies to GIF and PNG images; avoids blurry edges */
img[src$=".gif"], img[src$=".png"] {
    image-rendering: -moz-crisp-edges;         /* Firefox */
    image-rendering:   -o-crisp-edges;         /* Opera */
    image-rendering: -webkit-optimize-contrast;/* Webkit (non-standard naming) */
    image-rendering: crisp-edges;
