    <svg :width="width" :height="height" :fill="fill" class="icon">
      <use :xlink:href="`/wp-content/uploads/builderboy/assets/bby-icons.svg?ver=${appVersion}#` + name" />
    </svg>
  </div>
</template>

<script>
import {AppVersion} from "@/services/AppConfig.js"

export default {
  data () {
    return {
      appVersion: AppVersion.APP_VERSION
    }
  },
  props: {
    name: String,
    width: {
      type: [Number, String],
      default: 24
    },
    height: {
      type: [Number, String],
      default: 24
    },
    fill: {
      color: String,
    }
  },
};
