<template lang="html">
  <div class="bby-section container-fluid">
    <div class="bby-content">


      <div class="bby-single-tab">
          <div class="bby-components-wrapper">
            <div class="row justify-content-around">
              <div class="bby-components-left col-sm-8 col-xl-6">
                <div class="bby-box-margin bby-box-component">
                  <div class="bby-box bby-bg-primaryReverseWhite">
                    <div class="bby-box-install">
                      <h4>INSTALLATION PROGRESS</h4>
                      <div class="bby-box-install-progress">
                        <ul class="bby-style-flex bby-row bby-justify-center">
                          <li v-for="(tab, index) in tabs" :key="index" class="progress-step">{{tab}}</li>
                        </ul>
                      </div>
                      <div class="bby-box-install-status">
                          <div id="bby-box-install-status-box" class="bby-box-install-status-box">
                            <span class="bby-box-install-status-box-left">license status</span><span class="bby-box-install-status-box-right">{{licenseInfo}}</span>
                          </div>
                      </div>

                      <div class="progress-step-tab">
                        <div class="progress-step-tab-content">
                            <h3>Hello there!</h3>
                            <p>Nice to finally see you here, we’ve been waiting for you! You are only a few steps away from your totally new, breathtaking experience in designing logos for your venture.</p>
                            <h5>LET'S GET STARTED!</h5>

                            <p class="bby-logo-in-content"><BaseIconBby name="bbyLogoColor" height="43" width="200" /></p>
                        </div>
                      </div>

                      <div class="progress-step-tab">
                        <div class="progress-step-tab-content">
                          <div class="bby-box-install-license">
                            <h5>ENTER YOUR LICENSE CODE FROM CODECANYON.NET</h5>
                            <div class="bby-box-install-license-input">
                              <div class="bby-box-install-license-input-message">
                                <transition name="errMessage">
                                  <div v-if="errorText.baseMessage" class="input-message" :class="{active: licenseActive, inactive: !licenseActive}">
                                    {{errorText.baseMessage}}
                                  </div>
                                </transition>
                                <transition name="errMessage">
                                  <div v-if="errorText.requestId" class="input-message">
                                    {{errorText.requestId}}
                                  </div>
                                </transition>
                                <transition name="errMessage">
                                  <div v-if="errorText.emptyValue" class="input-message">
                                      {{errorText.emptyValue}}
                                  </div>
                                </transition>
                                <transition name="errMessage">
                                  <div v-if="errorText.wrongFormat" class="input-message">
                                      {{errorText.wrongFormat}}
                                  </div>
                                </transition>
                              </div>
                              <input
                                class="bby-input-text bby-bg-primaryReverseWhite bby-stroke-primaryMediumGray"
                                id="licenseCodeInput"
                                type="text"
                                placeholder="Your license code..."
                                @input="licenseCodeInput"
                                :value="licenseCode"
                                />
                              <div class="bby-button-license bby-button-verify">
                                <button id="buttonVerify" @click="licenseVerify(licenseCode)"></button>
                              </div>
                            </div>
                          </div>
                          <br/>
                          <p>OR</p>
                          <div>
                            <button @click="buyNow()" class="bby-button bby-button-medium bby-bg-primaryIndigo">BUY LICENSE</button>
                          </div>
                        </div>
                      </div>

                      <div class="progress-step-tab">
                        <div class="progress-step-tab-content">
                          <h5>Now we need to prepare few important things:</h5>
                          <div class="bby-box-install-elements">
                            <div v-for="(desc, index) in elementImport"  :key="index" class="bby-box-install-elements-single" id="bby-box-install-elements-single">
                                {{desc.title}} {{elementImportText}}
                            </div>
                          </div>
                        </div>
                      </div>

                      <div class="progress-step-tab">
                        <div class="progress-step-tab-content">
                            <h3>All done!</h3>
                            <h5>YOUR PLUGIN IS READY TO USE</h5>
                            <p>We hope you will have an amazing experience, just like we promised. If you want to share your thoughts about our products, don't hesitate to contact us!</p>
                            <p><BaseIconBby name="bbyLogoColor" height="43" width="200" /></p>
                        </div>
                      </div>

                    </div>
                    <div id="bby-box-install-buttons" class="bby-box-install-buttons">
                      <div class="bby-box-install-button-single">
                        <button class="bby-button-stroke bby-button-small bby-stroke-primaryGray" id="prev" @click="prevStep" v-if="prevButtonDisplay">BACK</button>
                      </div>
                      <div class="bby-box-install-button-single">
                        <button class="bby-button bby-button-medium bby-bg-primaryIndigo" id="next" @click="nextStep">{{nextStepButton}}</button>
                      </div>
                    </div>
                  </div>
                  <button class="bby-icon-button bby-no-bg skip-step" @click="nextStep" v-if="skipStep">SKIP THIS STEP</button>
                </div>
              </div>

              <div class="bby-components-right col-sm-8 col-xl-6">
                <div class="bby-box-margin bby-box-component">
                  <div class="bby-box bby-bg-primaryReverseWhite">

                    <div class="bby-newsletter bby-style-flex bby-column">
                      <div class="bby-style-flex bby-column">
                        <h4>NEWSLETTER</h4>
                        <p>Want to be up to date with our latest news? Leave your e-mail below.</p>
                      </div>
                      <div class="bby-style-flex bby-column" id="newsletter-info">
                        <div  class="bby-box-install-license-input-message">
                          <transition name="errMessage">
                            <div v-if="emailText.validEmail" class="input-message">
                              {{emailText.validEmail}}
                            </div>
                          </transition>
                          <transition name="errMessage">
                            <div v-if="emailText.errorEmail" class="input-message">
                              {{emailText.errorEmail}}
                            </div>
                          </transition>
                          <transition name="errMessage">
                            <div v-if="emailText.requestId" class="input-message">
                                {{emailText.requestId}}
                            </div>
                          </transition>
                        </div>
                        <div class="bby-style-flex bby-column bby-input-area">
                          <div class="bby-input-area-top bby-style-flex">
                            <input
                              class="bby-input-text bby-bg-primaryReverseWhite bby-stroke-primaryMediumGray"
                              id="newsletterInput"
                              type="text"
                              placeholder="Your e-mail"
                              @input="newsletterEmail"
                              :value="email"
                              />
                              <div class="bby-button-newsletter bby-button-verify">
                                <button id="verifyNewsletter" @click="buttonNewsletter(email)" class="disabled"></button>
                              </div>
                            </div>
                          </div>
                          <div class="bby-input-area-bottom bby-style-flex bby-row bby-center">
                            <input @input="checkboxNewsletter" type="checkbox" id="newsletter" name="" />
                            <label for="newsletter">Send me information about products, services, deals or recommendations by email.</label>
                          </div>
                      </div>
                    </div>

                  </div>
                  <div class="bby-box bby-bg-primaryReverseWhite">
                    <div class="bby-contact-box">
                      <div class="contact-box-text">
                        <h4>FIND US</h4>
                        <p>Check out our social media and stay in touch.</p>
                      </div>
                      <ul class="contact-box-list bby-style-flex bby-row">
                        <li>
                            <a :href="`${WebsiteUrl}/rd/facebook`"><BaseIconBby name="facebook" height="20" width="20" /></a>
                        </li>
                        <li>
                            <a :href="`${WebsiteUrl}/rd/instagram`"><BaseIconBby name="instagram" height="20" width="20" /></a>
                        </li>
                        <li>
                            <a :href="`${WebsiteUrl}/rd/behance`"><BaseIconBby name="behance" height="20" width="20" /></a>
                        </li>
                        <li>
                            <a :href="`${WebsiteUrl}/rd/contact`"><BaseIconBby name="bbyMail" height="20" width="20"/></a>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
      </div>

    </div>

    <div class="bby-install-feature row justify-content-around">
      <div class="bby-components-left col-sm-8 col-xl-6">

            <h3>OUR EXTENSIONS</h3>
            <div class="bby-install-feature-list bby-style-flex">
            <div class="bby-install-feature-list-single bby-box bby-bg-primaryReverseWhite">
              <div class="bby-install-feature-list-single-left">
                <p>READY</p>
                <div class="bby-feature-title">
                  <span class="bby-feature-title-name">LOGO</span>
                  <br>
                  <span class="bby-feature-title-subname">Builder</span>
                </div>
                <ul class="bby-feature-list bby-style-flex bby-justify-center bby-row">
                  <li>SVG/PNG</li>
                  <li>Limitless</li>
                </ul>
              </div>
              <div class="bby-install-feature-list-single-right">
                <span>Modern approach for creating amazing logos without expensive graphic software.</span>
                <div class="bby-install-feature-list-single-buy">
                  <button @click="buyNow()" class="bby-button bby-button-medium bby-bg-primaryIndigo">BUY NOW</button>
                </div>
              </div>
            </div>


            <div class="bby-install-feature-list-single bby-box bby-bg-primaryReverseWhite">
              <div class="bby-install-feature-list-single-left">
                <p>IN PROGRESS</p>
                <div class="bby-feature-title">
                  <span class="bby-feature-title-name">PERFORMANCE & SECURITY</span>
                  <br>
                  <span class="bby-feature-title-subname">Builder</span>
                </div>
                <ul class="bby-feature-list bby-style-flex bby-justify-center bby-row">
                  <li>Safe</li>
                  <li>Simple</li>
                </ul>
              </div>
              <div class="bby-install-feature-list-single-right">
                <span>Work fast & stay safe with your Wordpress and WooCommerce websites.</span>
                <div class="bby-install-feature-list-single-buy">
                  <button class="bby-button bby-button-small bby-stroke-primaryGray">AVAILABLE SOON</button>
                </div>
              </div>
            </div>

            <div class="bby-install-feature-list-single bby-box bby-bg-primaryReverseWhite">
              <div class="bby-install-feature-list-single-left">
                <p>IN PROGRESS</p>
                <div class="bby-feature-title">
                  <span class="bby-feature-title-name">HEADER & FOOTER</span>
                  <br>
                  <span class="bby-feature-title-subname">Builder</span>
                </div>
                <ul class="bby-feature-list bby-style-flex bby-justify-center bby-row">
                  <li>Intuitive</li>
                  <li>Lightweight</li>
                </ul>
              </div>
              <div class="bby-install-feature-list-single-right">
                <span>Build beautiful headers and footers without any programming knowledge.</span>
                <div class="bby-install-feature-list-single-buy">
                  <button class="bby-button bby-button-small bby-stroke-primaryGray">AVAILABLE SOON</button>
                </div>
              </div>
            </div>

            <div class="bby-install-feature-list-single bby-box bby-bg-primaryReverseWhite">
              <div class="bby-install-feature-list-single-left">
                <p>IN PROGRESS</p>
                <div class="bby-feature-title">
                  <span class="bby-feature-title-name">THEME</span>
                  <br>
                  <span class="bby-feature-title-subname">All in one</span>
                </div>
                <ul class="bby-feature-list bby-style-flex bby-justify-center bby-row">
                  <li>Powerful</li>
                  <li>Highend</li>
                </ul>
              </div>
              <div class="bby-install-feature-list-single-right">
                <span>All of our extensions in a single package. Made using cutting edge technologies.</span>
                <div class="bby-install-feature-list-single-buy">
                  <button class="bby-button bby-button-small bby-stroke-primaryGray">AVAILABLE SOON</button>
                </div>
              </div>
            </div>

          </div>
      </div>
      <div class="bby-components-right col-sm-8 col-xl-6">
        <img :src="`/wp-content/uploads/builderboy/assets/bby-builders-02.svg?ver=${appVersion}`" alt="">
      </div>
    </div>
  </div>
</template>

<script>
import {AppVersion} from "@/services/AppConfig.js"
import { mapState } from "vuex";
import axios from "axios";
import TabsService from "@/services/TabsService.js";
import {LambdaAPI} from "@/services/AppConfig.js"
import {NewsletterAPI} from "@/services/AppConfig.js"
import {WebsiteUrl} from "@/services/AppConfig.js"




export default {
  data() {
    return {
      appVersion: AppVersion.APP_VERSION,
      WebsiteUrl: WebsiteUrl.WEBSITE_URL,
      checkbox: false,
      licenseCode: '',
      count: 0,
      nextStepButton: 'NEXT',
      prevButtonDisplay: false,
      skipStep: false,
      licenseInfo: 'INACTIVE',
      elementImport:
      {
        saveIcon:
          {
            title: 'icons',
            active:false
          },
        saveGoogleFonts:
          {
            title: 'fonts',
            active: false
          },
        saveAssets:
          {
            title: 'assets',
            active: false
          }
      },
      elementImportText: '',
      errorText:{
        baseMessage: '',
        requestId: '',
        emptyValue: '',
        wrongFormat: ''
      },
      licenseActive: false,
      tabs:['WELCOME','LICENSE','INSTALLATION','COMPLETED'],
      email: '',
      emailText:{
        validEmail: '',
        errorEmail: '',
        requestId: ''
      }
    }
  },
  beforeMount(){
    if (this.licenseCodeState.license != '') {
      this.licenseVerify(this.licenseCodeState.license)
    }
  },
  mounted(){
    document.getElementsByClassName('progress-step')[0].classList.add('active')
    document.getElementsByClassName('progress-step-tab')[0].classList.add('active')
  },
  watch:{
    elementImport:{
      handler(val){
        var next = document.getElementById('next')
        var getKey = Object.keys(val)
        for (var i in getKey) {
          if (val[getKey[i]].active === false) {
            return ;
          }
        }
        next.classList.remove('disabled')
        next.disabled = false
      },
      deep: true
    },
    count(val, oldVal){
      var next = document.getElementById('next')
      var progressStep = document.getElementsByClassName('progress-step')
      var progressStepTab = document.getElementsByClassName('progress-step-tab')



      if (val >= (progressStep.length - 1)) {
        this.nextStepButton = 'BUILD YOUR LOGO'
      }
      else {
        this.nextStepButton = 'NEXT'
      }
      if (val > oldVal) {
        progressStep[val].classList.add('active')
        progressStepTab[val].classList.add('active')
        progressStepTab[oldVal].classList.remove('active')
      }
      if (val < oldVal) {
        progressStepTab[val].classList.add('active')
        progressStepTab[oldVal].classList.remove('active')
      }
      if (val != 0) {
        this.prevButtonDisplay = true
      }
      if (val === 1) {
        this.skipStep = true
        if (!this.licenseActive) {
          next.classList.add('disabled')
          next.disabled = true
        }
      }
      else {
        this.skipStep = false
        next.classList.remove('disabled')
        next.disabled = false
      }
      if (val <= 0) {
        this.prevButtonDisplay = false
      }
      if (val === 2) {
        let tsFunctions = ['saveIcon', 'saveGoogleFonts', 'saveAssets']
        for (var i in tsFunctions) {
          if(!this.elementImport[tsFunctions[i]].active){
            var installSingleName = document.getElementsByClassName('bby-box-install-elements-single')
            installSingleName[i].classList.add('installActive')
            next.classList.add('disabled')
            next.disabled = true
            this.elementImportText = ''
            setTimeout((i) => {
              TabsService[tsFunctions[i]]()
              .then(response => {
                if (response.status === 200) {
                  this.elementImportText = 'ready!'
                  installSingleName[i].classList.remove('installActive')
                  installSingleName[i].classList.add('installed')
                  this.elementImport[tsFunctions[i]].active = true
                }
              })
            },3000, i)
          }
        }
      }
    },
    licenseActive(val){
      var next = document.getElementById('next')
      var activePlugin = document.getElementById('bby-box-install-status-box')
      if (val) {
        next.classList.remove('disabled')
        next.disabled = false
        this.licenseInfo = 'ACTIVE'
        activePlugin.classList.add('activePlugin')
      }
      else {
        next.classList.add('disabled')
        next.disabled = true
        this.licenseInfo = 'INACTIVE'
        activePlugin.classList.remove('activePlugin')
      }
    },
    checkbox(val){
      var verifyNewsletter = document.getElementById('verifyNewsletter')
      if (val) {
        verifyNewsletter.classList.remove('disabled')
      }
      else {
        verifyNewsletter.classList.add('disabled')
      }
    }
  },
  methods: {
    checkboxNewsletter(event){
      this.checkbox = event.target.checked
    },
    buyNow(){
      window.open(`${WebsiteUrl.WEBSITE_URL}/rd/logobuilder/buyLicense`,"_self")
    },
    updateValues(){
      if (this.licenseActive) {
        this.$store.commit("UPDATE_INSTALLATION", [{value: {license: this.licenseCode,init: "false"}, name: this.contentTabs.adminSettings.name}])
      }
      else {
        this.$store.commit("UPDATE_INSTALLATION", [{value: {license: '',init: "false"}, name: this.contentTabs.adminSettings.name}])
      }
      this.$store.commit('replaceValues')
    },
    nextStep(){
      var progressStep = document.getElementsByClassName('progress-step')
      this.count++
      if (this.count >= progressStep.length) {
        this.updateValues()
      }
    },
    prevStep(){
      var progressStep = document.getElementsByClassName('progress-step')
      progressStep[this.count].classList.remove('active')
      this.count--
    },
    licenseCodeInput(event){
      this.licenseCode = event.target.value
      if (this.licenseCode != 0) {
        this.errorText.emptyValue = ''
      }
    },
    newsletterEmail(event){
      this.email = event.target.value
    },
    callbackSuccess(verify) {
      var newsletterInfo = document.getElementById('newsletter-info')
      setTimeout(() => {
        verify.classList.remove("validate")
        verify.classList.remove("invalidate")
        this.errorText.baseMessage = ''
        this.emailText.validEmail = ''
        newsletterInfo.classList.remove('emailSuccess')
      }, 2000)
    },
    callbackError(verify, input) {
      var newsletterInfo = document.getElementById('newsletter-info')
      setTimeout(() => {
        verify.classList.remove("validate")
        verify.classList.remove("invalidate")
        input.classList.remove('error')
        this.emailText.errorEmail = ''
        this.emailText.requestId = ''
        newsletterInfo.classList.remove('emailError')
        this.licenseError = true
        this.errorText.requestId = ""
      }, 2000)
    },
    encodeHtmlChars(str) {
      var i = str.length,
      result = [];
      while (i--) {
        var charCode = str[i].charCodeAt();
        if (charCode < 48 || (charCode > 57 && charCode < 65) || (charCode > 90 && charCode < 97) || charCode > 123) {
          result[i] = '&#'+charCode+';';
        } else {
          result[i] = str[i];
        }
      }
      return result.join('');
    },
    buttonNewsletter(email){
      var newsletterInfo = document.getElementById('newsletter-info')
      var verifyNewsletter = document.getElementById('verifyNewsletter')
      var newsletterInput = document.getElementById('newsletterInput')

      if(this.email != "" && this.checkbox){
        const regexEmail = new RegExp(/^[a-zA-Z0-9.!#$%&'*+\=?^_`{|}~-]+@[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?(?:\.[a-zA-Z0-9](?:[a-zA-Z0-9-]{0,61}[a-zA-Z0-9])?)*$/gm)
        if (regexEmail.test(email)) {
          var dateTime = new Date;
          var encodeEmail = this.encodeHtmlChars(email)
          verifyNewsletter.classList.add("onclick")
          axios.post(NewsletterAPI.API_NEWSLETTER_ENDPOINT + 'newsletter/email/add',
          {
            "email": encodeEmail,
            "date": dateTime,
            "appVersion": this.appVersion,
            "product":"logoBuilder"
          },
          { headers:
            {
              "Content-Type": "application/json"
            }
          })
          .then(response =>{
            if (response.status == '200') {
              setTimeout(() => {
                this.emailText.validEmail = response.data.message
                newsletterInfo.classList.add('emailSuccess')
                verifyNewsletter.classList.remove("onclick")
                verifyNewsletter.classList.add("validate")
                this.callbackSuccess(verifyNewsletter)
              },3000)
            }
          })
          .catch(error =>{
            setTimeout(() => {
              newsletterInfo.classList.add('emailError')
              this.emailText.errorEmail = error.response.data.message
              this.emailText.requestId = "Your request ID: "+error.response.data.requestId

              verifyNewsletter.classList.remove("onclick")
              verifyNewsletter.classList.add("invalidate")
              newsletterInput.classList.add('error')
              this.callbackError(verifyNewsletter, newsletterInput)
            },3000)
          })
        }
        else {
          newsletterInfo.classList.add('emailError')
          this.emailText.errorEmail = 'Email address is not valid. Please verify.'
          setTimeout(() => {
            this.emailText.errorEmail = ''
            newsletterInfo.classList.remove('emailError')
          },3000)
        }
      }
      else {
        if(this.email === ''){
          newsletterInfo.classList.add('emailError')
          this.emailText.errorEmail = 'Value cannot be empty.'
          setTimeout(() => {
            this.emailText.errorEmail = ''
            newsletterInfo.classList.remove('emailError')
          },3000)
        }
        if (this.checkbox === false) {
          newsletterInfo.classList.add('emailError')
          this.emailText.errorEmail = 'You need to agree to our Privacy Policy by clicking checkbox below.'
          setTimeout(() => {
            this.emailText.errorEmail = ''
            newsletterInfo.classList.remove('emailError')
          },3000)
        }
      }
    },
    licenseVerify(licenseValue){
      var verify = document.getElementById('buttonVerify')
      if(this.licenseCode != ""){
        const regex = new RegExp(/^([a-f0-9]{8})-(([a-f0-9]{4})-){3}([a-f0-9]{12})$/);
        if (regex.test(this.licenseCode)){
          verify.classList.add("onclick")
          var encodeLicenseValue = this.encodeHtmlChars(licenseValue)
          TabsService.checkLicenseActive(encodeLicenseValue)
          .then(response => {
              if (response.status == '200') {
                setTimeout(() => {
                  this.errorText.baseMessage = response.data.message
                  this.licenseActive = true
                  verify.classList.remove("onclick")
                  verify.classList.add("validate")
                  this.callbackSuccess(verify)
                  this.$store.commit('LICENSE_STATUS', {active: true})
                  this.$store.commit("UPDATE_COMPONENT_VALUES", [{value: {license: licenseValue, init: "false"}, name: this.contentTabs.adminSettings.name}]);
                }, 2000)
              }
          })
          .catch(error =>{
            var input = document.getElementById('licenseCodeInput')
            if (error.response.data.error) {
              setTimeout(() => {
                this.licenseActive = false
                this.errorText.baseMessage = error.response.data.message
                this.errorText.requestId = "Your request ID:" + error.response.data.requestId
                verify.classList.remove("onclick")
                verify.classList.add("invalidate")
                input.classList.add('error')
                this.callbackError(verify, input)
                this.$store.commit('LICENSE_STATUS', {active: false})
                this.$store.commit("UPDATE_COMPONENT_VALUES", [{value: {license: '', init: "true"}, name: this.contentTabs.adminSettings.name}]);
              }, 3000)
            }
          })
        }
        else {
          this.errorText.wrongFormat = 'Wrong license format.'
          setTimeout(() => {
            this.errorText.wrongFormat = ''
          },3000)
          this.$store.commit('LICENSE_STATUS', {active: false})
        }
      }
      else {
        this.errorText.emptyValue = 'Value cannot be empty.'
      }
    }
  },
  computed: {
    ...mapState({
      contentTabs: state => state.contentTabs,
      licenseCodeState: state => state.contentTabs.adminSettings.selected_value,
    })
  },
};
</script>

<style lang="css" scoped>

.bby-input-area-bottom{
  margin-top: .5rem
}

.bby-install-feature-list-single-buy{
