<template lang="html">
  <transition name="modal">
    <div class="bby-modal-open bby-style-flex bby-column bby-center bby-justify-center" v-show="modalDisplay">

        <div class="bby-modal-bg" id="bby-modal-bg"></div>
        <div class="bby-modal-inner" id="bby-modal-inner">

          <div class="bby-close-modal" @click="closeModal" >
            <BaseIconBby name="bbyDel" width="20" height="20" />
          </div>

          <div class="bby-modal-inner-wrap">
            <div class="bby-modal-inner-content bby-style-flex bby-center bby-column">
              <h5>GET AN ACCESS TO ALMOST 350,000 ICONS AND PLUGIN ADVANCED SETTINGS</h5>
              <div class="bby-install-feature-list-single-buy">
                <button @click="buyNow()" class="bby-button bby-button-medium bby-bg-primaryIndigo">BUY NOW</button>
              </div>
              <p>or</p>
              <h5>ENTER YOUR LICENSE CODE FROM THEMEFOREST</h5>
              <div class="bby-box-install-license-input">
                <div class="bby-box-install-license-input-message">
                  <transition name="errMessage">
                    <div v-if="errorText.baseMessage" class="input-message" :class="{active: licenseActive, inactive: !licenseActive}">
                      {{errorText.baseMessage}}
                    </div>
                  </transition>
                  <transition name="errMessage">
                    <div v-if="errorText.requestId" class="input-message">
                      {{errorText.requestId}}
                    </div>
                  </transition>
                  <transition name="errMessage">
                    <div v-if="errorText.emptyValue" class="input-message">
                        {{errorText.emptyValue}}
                    </div>
                  </transition>
                  <transition name="errMessage">
                    <div v-if="errorText.wrongFormat" class="input-message">
                        {{errorText.wrongFormat}}
                    </div>
                  </transition>
                </div>
                <input
                  class="bby-input-text bby-bg-primaryReverseWhite bby-stroke-primaryMediumGray"
                  id="licenseCodeInput"
                  type="text"
                  placeholder="Your license code..."
                  @input="licenseCodeInput"
                  :value="licenseCode"
                  />
                <div class="bby-button-license bby-button-verify">
                  <button id="buttonModalVerify" @click="licenseVerify(licenseCode)"></button>
                </div>
              </div>
            </div>
          </div>


        </div>
    </div>
  </transition>
</template>

<script>
import { mapState } from "vuex";
import axios from "axios";
import TabsService from "@/services/TabsService.js";
import {LambdaAPI} from "@/services/AppConfig.js"
import {WebsiteUrl} from "@/services/AppConfig.js"

export default {
  data () {
    return {
      errorText:{
        baseMessage: '',
        requestId: '',
        emptyValue: '',
        wrongFormat: ''
      },
      licenseCode: '',
    }
  },
  props:{
    modalDisplay: Boolean
  },
  updated(){
      var modal = document.getElementById('bby-modal-inner')
      var modalHeight = modal.offsetHeight/2
      modal.style.marginTop = -modalHeight+'px'
  },
  watch:{
    modalDisplay(val){
      var contentModal = document.getElementById('bby-single-tab')
      var modalBg = document.getElementById('bby-modal-bg')
      var modalInner = document.getElementById('bby-modal-inner')
      modalBg.onclick = ((e)=>{
        if(e.target != modalInner) {
          this.closeModal()
        }
      })
      if (val) {
        contentModal.classList.add('modalOpen')
      }
      else {
        setTimeout(()=>{
          contentModal.classList.remove('modalOpen')
        },500)
      }
    }
  },
  methods:{
    buyNow(){
      window.open(`${WebsiteUrl.WEBSITE_URL}/rd/logobuilder/buyLicense`,"_self")
    },
    closeModal(){
      this.$emit('clicked')
    },
    licenseCodeInput(event){
      this.licenseCode = event.target.value
      if (this.licenseCode != 0) {
        this.errorText.emptyValue = ''
      }
    },
    encodeHtmlChars(str) {
      var i = str.length,
      result = [];
      while (i--) {
        var charCode = str[i].charCodeAt();
        if (charCode < 48 || (charCode > 57 && charCode < 65) || (charCode > 90 && charCode < 97) || charCode > 123) {
          result[i] = '&#'+charCode+';';
        } else {
          result[i] = str[i];
        }
      }
      return result.join('');
    },
    callbackSuccess(verify) {
      setTimeout(() => {
        verify.classList.remove("validate")
        verify.classList.remove("invalidate")
        this.errorText.baseMessage = ''
      }, 2000)
    },
    callbackError(verify, input) {
      setTimeout(() => {
        verify.classList.remove("validate")
        verify.classList.remove("invalidate")
        input.classList.remove('error')
        this.licenseError = true
        this.errorText.requestId = ""
      }, 2000)
    },
    licenseVerify(licenseValue){
      var verify = document.getElementById('buttonModalVerify')

      if(this.licenseCode != ""){
        const regex = new RegExp(/^([a-f0-9]{8})-(([a-f0-9]{4})-){3}([a-f0-9]{12})$/);
        if (regex.test(this.licenseCode)){
          verify.classList.add("onclick")

          var encodeLicenseValue = this.encodeHtmlChars(licenseValue)
        TabsService.checkLicenseActive(encodeLicenseValue)
          .then(response =>{
            if (response.status == '200') {
              setTimeout(() => {
                this.errorText.baseMessage = response.data.message
                this.licenseActive = true
                verify.classList.remove("onclick")
                verify.classList.add("validate")
                this.callbackSuccess(verify)
                this.$store.commit('LICENSE_STATUS', {active: true})
                this.$store.commit("UPDATE_INSTALLATION", [{value: {license: licenseValue, init: "false"}, name: 'licenseVerification'}]);
                this.$store.commit('replaceValues')
              }, 3000)
            }
          })
          .catch(error =>{
            var input = document.getElementById('licenseCodeInput')
            if (error.response.data.error) {
              setTimeout(() => {
                this.licenseActive = false
                this.errorText.baseMessage = error.response.data.message
                this.errorText.requestId = "Your request ID:" + error.response.data.requestId
                verify.classList.remove("onclick")
                verify.classList.add("invalidate")
                input.classList.add('error')
                this.callbackError(verify, input)
                this.$store.commit('LICENSE_STATUS', {active: false})
                this.$store.commit("UPDATE_INSTALLATION", [{value: {license: '', init: "false"}, name: 'licenseVerification'}]);
                this.$store.commit('replaceValues')
              }, 3000)
            }
          })
        }
        else {
          this.errorText.wrongFormat = 'Wrong license format.'
          setTimeout(() => {
            this.errorText.wrongFormat = ''
          },3000)
          this.$store.commit('LICENSE_STATUS', {active: false})
        }
      }
      else {
        this.errorText.emptyValue = 'Value cannot be empty.'
      }
    }
  }
}
</script>

