<template lang="html">
  <div class="bby-column">
    <div class="footer container-fluid">
      <footer class="row">
        <div class="bby-footer col-sm bby-style-flex bby-space-between bby-center">
          <div class="bby-footer-copyright">
            © BuilderBoy.co. All rights reserved.
          </div>
          <ul class="bby-footer-social-contact bby-row bby-center">
            <li>
                <a :href="`${WebsiteUrl}/rd/facebook`"><BaseIconBby name="facebook" height="20" width="20" fill="#C2C2C2" /></a>
            </li>
            <li>
                <a :href="`${WebsiteUrl}/rd/instagram`"><BaseIconBby name="instagram" height="20" width="20" fill="#C2C2C2"/></a>
            </li>
            <li>
                <a :href="`${WebsiteUrl}/rd/behance`"><BaseIconBby name="behance" height="20" width="20" fill="#C2C2C2"/></a>
            </li>
            <li>
                <a :href="`${WebsiteUrl}/rd/contact`"><BaseIconBby name="bbyMail" height="20" width="20" fill="#C2C2C2"/></a>
            </li>
          </ul>
          <button class="bby-button bby-button-medium  bby-bg-primaryIndigo" @click="updateValues()">Save changes</button>
        </div>
      </footer>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import sendNotification from "@/services/NotificationService.js";
import {WebsiteUrl} from "@/services/AppConfig.js"


export default {
  data () {
    return {
      formError: false,
      WebsiteUrl: WebsiteUrl.WEBSITE_URL
    }
  },
  methods: {
    updateValues(){
      for (let i in this.componentErrorsState){
        this.formError = this.componentErrorsState[i]
        if (this.formError) break
      }

      if (this.formError) {
        sendNotification('Ooops some fields are incorrect!', 'errors')
      } else {
        this.$store.commit('replaceValues')
      }
    }
  },
  computed:{
    ...mapState({
      componentErrorsState: state => state.componentErrors
      })
  }
};
</script>

<style lang="css" scoped>

</style>
