<template lang="html">
  <div class="row" id="previewRow">
    <div class="bby-components-left col-sm" id="previewColLeft" v-for="(firstComponentProp, index) in firstComponentProps.items"  :key="index">

      <div class="bby-png-generator">
        <!-- <input :value=imgURL.ImageURL /> -->

        <!-- Hidden elements for generating logo in PNG START -->
        <span><img id="img" style="display: none"/></span>
        <canvas id="canvas" style="display: none"></canvas>
        <!-- END -->

      </div>

      <div v-for="(option, index) in firstComponentProp.options" :key="index" class="bby-components-group">
        <h4>{{option.propertyName}}</h4>
        <component
          :is="option.component.type"
          :firstComponentProps="option.component"
          :title="option.propertyName"
        ></component>
      </div>
    </div>
    <div class="bby-components-right col-sm">
      <BaseFunctionalLogoPreview @generate="generatePNG()" :logoUrl="imgURL.ImageURL" />
    </div>
  </div>
</template>

<script>
import TabsService from "@/services/TabsService.js";
import sendNotification from "@/services/NotificationService.js";
import axios from "axios";
import {WordpressConfig} from "@/services/AppConfig.js"



export default {
  data () {
    return {
      imgURL: '',
      logoPNG: ''
    }
  },
  props: {
    firstComponentProps: Object
  },

  watch:{
    logoPNG(val){
      var that = this;
      axios.post(WordpressConfig.WP_MAIN_PAGE_URL + '&action=uploadGeneratedLogo',
      val,
      { headers: TabsService.getAxiosHeadersConfig() })
    .then(function (response) {
      that.imgURL = response.data;
      let status = response.data
      let notificationType = ''
      if (status.error){
        notificationType = 'error'
      } else {
        notificationType = 'success'
      }
      sendNotification(status.message, notificationType)
    })
    .catch(function (error) {
      console.log(error);
    });
    }

  },
  methods:{
    generatePNG(){
      var svg = document.getElementById('svgForPreview');
      var img = document.getElementById('img');
      var canvas = document.getElementById('canvas');
      var newIMG = document.getElementById('newIMG');

      var xml = new XMLSerializer().serializeToString(svg);
      var svg64 = btoa(unescape(encodeURIComponent(xml)));
      var b64Start = 'data:image/svg+xml;base64,';
      var image64 = b64Start + svg64;
      var that = this
      var imgClone = img.cloneNode(true);

      imgClone.onload = function() {
      document.fonts.ready.then(() => {
        setTimeout(() => {
          var canvasClone = canvas.cloneNode(true);
          canvasClone.style.display = 'inline-block'
          imgClone.style.display = 'inline-block'
          canvasClone.width = (svg.getAttribute('width')) * 12;
          canvasClone.height = (svg.getAttribute('height')) * 12;
          // canvasClone.style.width = svg.getAttribute('width') + "px";
          // canvasClone.style.height = svg.getAttribute('height') + "px";

          var ctx = canvasClone.getContext('2d')
          ctx.scale(12,12)
          ctx.drawImage(imgClone, 0, 0);
          that.logoPNG = canvasClone.toDataURL("image/png")
        }, 700)
          })
      }
      imgClone.src = image64;
    }
  }
}
</script>

<style lang="css" scoped >

.newIMG{
  image-rendering: -moz-crisp-edges; /* Firefox */
  image-rendering: -o-crisp-edges; /* Opera */
  image-rendering: -webkit-optimize-contrast; /* Webkit (non-standard naming) */
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor; /* IE (non-standard property) */
}

</style>
