<template lang="html">
  <div class="bby-functional-components">
    <div class="bby-box bby-bg-primaryReverseWhite">

      <div class="bby-box-component-title bby-style-flex bby-center">
        <h5>{{title}}</h5>
        <div class="bby-heading-hint">
          <BaseHint :hintValue="'Once enabled you will be able to use icon in the logo'" :componentTitle="title"></BaseHint>
        </div>
      </div>

      <label :for="firstComponentProps.name">
        <div class="switch-box-wrapper">
          <div class="switch-box"
            :class="styleCheckbox()">
            <div class="switch-box-circle">
            </div>
          </div>
        </div>
      </label>
      <input hidden :id="firstComponentProps.name" type="checkbox" name="isIconTextLogoFunctionalLogoText" @change="haveIcon"/>
    </div>

    <transition
    name="expand"
    @enter="enter"
    @after-enter="afterEnter"
    @leave="leave"
    >

      <div v-show="isLogoIcon">
        <div v-for="(firstComponentProp, index) in firstComponentProps.items"  :key="index" class="bby-box bby-bg-primaryReverseWhite" :class="error(firstComponentProp.component.name)">
          <component
            :is="firstComponentProp.component.type"
            :firstComponentProps="firstComponentProp.component"
            :title="firstComponentProp.propertyName"
          ></component>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import _ from 'lodash';
import internalHandlers from "@/services/internalHandlers.js";

export default {
  data () {
    return {
      isLogoIcon: '',
      title: 'use icon for your logo?'
    }
  },
  props: {
    firstComponentProps: Object,
  },
  beforeMount(){
    if (this.$store.state.values.logoFunctionalLogoIcon == null) {
      let iconSelectedValue = JSON.parse(JSON.stringify(this.firstComponentProps.selected_value))
      // iconSelectedValue.iconSvgCode = iconSelectedValue.iconSvgCode.replace(/\"/g, "\\\"")
      iconSelectedValue.iconSvgCode = iconSelectedValue.iconSvgCode

      this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: iconSelectedValue}])
    } else {
        this.isLogoIcon = this.logoIconUse.iconUse == "true";
    }

  },
  methods:{
    styleCheckbox(){
      if (this.isLogoIcon) {
        return 'switchBoxOn'
      }
      else {
        return 'switchBoxOff'
      }
    },
    error(value){
      if (this.$store.state.componentErrors[value]) {
        return 'bby-box-error'
      }
    },
    haveIcon(event){
      this.isLogoIcon = !this.isLogoIcon
      this.$store.commit("UPDATE_ICON_CHECKBOX", [{name: this.firstComponentProps.name, value: {iconUse: ""+this.isLogoIcon}}]);
    },
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    }
  },
  watch:{
    logoIconTypeState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: newval}])
      }
    },
    logoIconSizeState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {iconSize: newval}}])
      }
    },
    logoIconColorState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {iconColor: newval}}])
      }
    },
    'logoIconUse.iconUse': function (newval, oldval){
      if (!_.isEqual(newval, oldval)){
        if (newval == 'true'){
          this.isLogoIcon = true
        } else {
          this.isLogoIcon = false
        }
      }
    },
    logoIconTopOffsetState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {iconTopOffset: newval}}])
      }
    },
    logoIconLeftOffsetState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {iconLeftOffset: newval}}])
      }
    },
    logoIconPositionState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {iconPosition: newval}}])
      }
    },
    logoIconOwnSVGState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        if (!!newval){
          this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {iconSvgCode: newval}}])
        }
      }
    },
    logoIconUseOwnSVGState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        if (newval.toLowerCase() === 'true' && this.logoIconOwnSVGState){
          this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: {iconSvgCode: this.logoIconOwnSVGState}}])
        } else {
          this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: this.logoIconTypeState}])
        }
      }
    }
  },
  computed:{
    ...mapState({
      logoIconTypeState: state => state.values.iconFunctionalLogoIcons,
      logoIconSizeState: state => state.values.iconSizeRangeSlider,
      logoIconColorState: state => state.values.iconColorColorPicker,
      logoIconUse: state => state.values.logoFunctionalLogoIcon,
      logoIconTopOffsetState: state => state.values.iconTopOffsetRangeSlider,
      logoIconLeftOffsetState: state => state.values.iconLeftOffsetRangeSlider,
      logoIconPositionState: state => state.values.iconPositionRadioButton,
      logoIconUseOwnSVGState: state => state.values.iconOwnSVGCheckbox,
      logoIconOwnSVGState: state => state.values.iconOwnSVGTextArea,
      showError: state => state.componentErrors.iconSizeRangeSlider
    })
  }
}
</script>

<style lang="scss" scoped >

.bby-box-component-title{
  margin-bottom: 1rem
}

.bby-box-component-title h5{
  margin-bottom: 0 !important
}

.bby-box-component-title .bby-heading-hint{
  margin-left: 5px;
}
</style>
