<template lang="html">
  <div class="bby-box-component">

    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>

    <transition
      name="expand"
      @enter="enter"
      @after-enter="afterEnter"
      @leave="leave"
    >
      <div class="bby-range-slider-error bby-error" v-show="showError">
        <p>This value needs to be between {{ rangeSliderMinValue }} and {{ rangeSliderMaxValue }} </p>
      </div>
    </transition>

    <div class="bby-component bby-range-slider">
      <div v-for="(firstComponentProp, index) in firstComponentProps.items"  :key="index" class="bby-component-wrapper" :class="{inputError: showError}">
        <input
          class="bby-range-slider-range"
          type="range"
          @input="rangeSlider"
          :name="firstComponentProps.name"
          :value="rangeSliderValue"
          :style="changeStyle"
          :min="firstComponentProp.min"
          :max="firstComponentProp.max"
          :step="firstComponentProp.step"
        />

        <input
          class="bby-range-slider-input bby-input-text  bby-bg-primaryReverseWhite bby-stroke-primaryMediumGray"
          type="number"
          @input="rangeSlider"
          :name="firstComponentProps.name"
          :value="rangeSliderValue"
          :min="firstComponentProp.min"
          :max="firstComponentProp.max"
        />

      </div>

    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex';
import _ from 'lodash';

export default {
  data() {
    return {
      rangeSliderValue:'',
      rangeSliderMinValue: '',
      rangeSliderMaxValue: '',
      showError: false
    };
  },
  props: {
    firstComponentProps: Object,
    title: String
  },
  beforeMount(){
    if (this.$store.state.values[this.firstComponentProps.name] == null) {
      this.$store.commit("UPDATE_COMPONENT_VALUES", [{name: this.firstComponentProps.name, value: this.firstComponentProps.selected_value}])
    }
    this.rangeSliderValue = this.rangeSliderState
    this.rangeSliderRanges()
  },
  watch:{
    rangeSliderValue:
      _.throttle(function(val){
        if (val <= this.rangeSliderMaxValue && val >= this.rangeSliderMinValue){
          if (val == '') val = 0
          this.$store.commit("UPDATE_COMPONENT_VALUES", [{value: val, name: this.firstComponentProps.name}]);
        }
      }, 5 ),
    showError(val){
      this.$store.commit("UPDATE_COMPONENT_ERRORS", [{value: val, name: this.firstComponentProps.name}]);
    }
  },
  methods:{
    rangeSliderRanges(){
      for (var i in this.firstComponentProps.items) {
        this.rangeSliderMinValue = this.firstComponentProps.items[i].min
        this.rangeSliderMaxValue = this.firstComponentProps.items[i].max
      }
    },
    rangeSlider(event){
      this.rangeSliderValue = event.target.value
      if (event.target.value <= this.rangeSliderMaxValue && event.target.value >= this.rangeSliderMinValue && event.target.value != ''){
        this.showError = false;
      } else {
        this.showError = true;
      }
    },
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
       el.addEventListener('scroll', this.handleScroll)

    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    },
  },
  computed:{
    changeStyle(){
      let moveRangeSlider
      if (this.rangeSliderMinValue >= 0) {
        moveRangeSlider = (this.rangeSliderValue-this.rangeSliderMinValue)/(this.rangeSliderMaxValue-this.rangeSliderMinValue)*100;
        if (moveRangeSlider < 0) moveRangeSlider = 0
      }
      else {
        return {background: '#cccccc'}
      }
      return {background: 'linear-gradient(to right, #02f7f8 0%, #02f7f8 ' + moveRangeSlider + '%, #cccccc ' + moveRangeSlider + '%, #cccccc 100%)'}
    },
    ...mapState({
      rangeSliderState (state){
        return state.values[this.firstComponentProps.name]
      }
    }),
  }
};


</script>

<style lang="css" scoped>

.bby-range-slider-range{
  border: 0 !important;
  border-radius: 16px;
  height: 6px;
  width: 100%;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  margin-right: 15px;
}

.bby-range-slider-input{
  width: max-content !important;
}

</style>
