<template lang="html">
  <div class="bby-box-component">

    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>

    <div class="bby-component bby-radio">
      <div class="bby-component-wrapper">
        <div v-for="(firstComponentProp, index) in firstComponentProps.items" :key="index">
          <input
            class="bbyRadio"
            type="radio"
            :id="firstComponentProp.value"
            :checked="checkedInputValue(firstComponentProp.value)"
            :name="firstComponentProps.name"
            :value="firstComponentProp.value"
            @change="updateInput"
          />
          <label :for="firstComponentProp.value" :id="firstComponentProp.value+'-label'">
              <BaseIconBby :name="firstComponentProp.iconSVG" width="105" height="105" />
          </label>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    firstComponentProps: Object,
    title: String
  },
  beforeMount(){
    if (this.$store.state.values[this.firstComponentProps.name] == null) {
      this.$store.commit("UPDATE_COMPONENT_VALUES", [{name: this.firstComponentProps.name, value: this.firstComponentProps.selected_value}])
    }
  },
  computed:{
    ...mapState({
      radioButtonState (state){
        return state.values[this.firstComponentProps.name]
      }
    })
  },
  methods:{
    updateInput(event){
      this.$store.commit("UPDATE_COMPONENT_VALUES", [{value: event.target.value, name: event.target.name}]);
    },
    checkedInputValue(value){
      if (value === this.radioButtonState) {
        return 'checked'
      }
      else {
        return ''
      }
    },
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    }
  }
};
</script>

<style lang="css" scoped>

.expand-enter-active, .expand-leave-active {
  transition: height .3s ease-in-out;
  overflow: hidden;
}

.bbyRadioFlex {
  display: flex;
  flex-direction: row;
}

.bbyRadio {
  display: none;
}

</style>
