<template lang="html">
  <div class="bby-style-flex bby-center">
    <span class="bby-hint-pointer bby-style-flex bby-center" @click="showHint = !showHint">
      <div class="bby-hint-place bby-hint-icon">
        <BaseIconBby :name="iconHintName" width="18" height="18" :fill="fill" />
      </div>
      <div class="bby-hint-place bby-hint-content">
        <transition name="fromLeft">
          <div class="bby-hint-content-open" v-if="showHint">{{hintValue}}
            <div v-if="componentTitle">Read more in <a :href="componentDocUrl" target="_blank" rel="noopener">documentation</a></div>
          </div>
        </transition>
      </div>
    </span>
  </div>
</template>

<script>

import internalHandlers from "@/services/internalHandlers.js";
import {DocumentationURL} from "@/services/AppConfig.js";

export default {
  props:{
    hintValue: String,
    iconHintName: {
      type: String,
      default: "bbyInfo"
    },
    componentTitle: {
      type: String,
      default: ""
    },
    fill: {
      color: String,
      default: "#444444"
    }
  },
  data() {
    return {
      componentDocUrl: '',
      showHint: false
    }
  },
  created(){
    if (!!this.componentTitle){
      this.componentDocUrl = `${DocumentationURL.APP_DOCS_URL}#${internalHandlers.slugify(this.componentTitle)}`
    }
  }
}
</script>

<style lang="css" scoped >

.bby-hint-place.bby-hint-content{
  position: absolute;
  margin-left: 20px;
  font-size: 10px;
}

.bby-hint-pointer{
  cursor: pointer;
}

.bby-hint-content-open{
  padding: 10px;
  box-shadow: 0 0 16px 0 rgba(0, 0, 0, 0.16);
  background-color: rgba(255, 255, 255, 0.8);
  margin-left: 15px;
}

</style>
