import Vue from "vue";

const JSON_HOST_ADDRESS = process.env.VUE_APP_JSON_HOST || "";
const WP_MAIN_PAGE_URL = process.env.VUE_APP_WP_MAIN_PAGE_URL;
const JSON_GOOGLE_FONTS = WP_MAIN_PAGE_URL + '&action=getFonts';
const JSON_GOOGLE_ICONS = WP_MAIN_PAGE_URL + '&action=getIcons';
const JSON_HOST_PATH = WP_MAIN_PAGE_URL + '&action=getJSONvalues' || "/";
const APP_VERSION = process.env.VUE_APP_VERSION;
const APP_DOCS_URL = process.env.VUE_APP_DOCS;

// Lambda API
const API_LOGO_ENDPOINT = process.env.VUE_APP_API_LOGO_ENDPOINT;
// Newsletter API
const API_NEWSLETTER_ENDPOINT = process.env.VUE_APP_API_NEWSLETTER_ENDPOINT;
// Buy license URL
const WEBSITE_URL = process.env.VUE_APP_WEBSITE_URL;

export const WordpressConfig = {
  JSON_HOST_ADDRESS : JSON_HOST_ADDRESS,
  JSON_HOST_PATH : JSON_HOST_PATH,
  WP_MAIN_PAGE_URL : WP_MAIN_PAGE_URL,
  JSON_GOOGLE_FONTS : JSON_GOOGLE_FONTS,
  JSON_GOOGLE_ICONS : JSON_GOOGLE_ICONS
}

export const LambdaAPI = {
  API_LOGO_ENDPOINT : API_LOGO_ENDPOINT
}

export const NewsletterAPI = {
  API_NEWSLETTER_ENDPOINT : API_NEWSLETTER_ENDPOINT
}

export const AppVersion = {
  APP_VERSION : APP_VERSION
}

export const WebsiteUrl = {
  WEBSITE_URL: WEBSITE_URL
}

export const DocumentationURL = {
  APP_DOCS_URL : APP_DOCS_URL
}
