<template lang="html">
  <div class="bby-box-component">

    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>

      <div v-html="ifIconsSprite"></div>
      <div class="bby-component bby-dropdown">
        <div @click="toggleListShow = !toggleListShow" ref="toggle" class="bby-f-dropdown-input bby-stroke-primaryMediumGray">
          <input
            type="text"
            readonly
            class="bby-functional-input"
            placeholder="Choose the icon"
          >
          <span><BaseIconBby name="bbyDown" width="12" height="12" /></span>
        </div>

        <transition
        name="expand"
        @enter="enter"
        @after-enter="afterEnter"
        @leave="leave"
        >
          <div class="bby-f-dropdown" v-if="toggleListShow" v-closable="{ exclude: ['toggle'], handler: 'onClose' }">
            <div class="bby-f-dropdown-input-search">
              <input
                type="text"
                @input="searchIconsInput"
                placeholder="Type here to search for the icon..."
                class="bby-f-dropdown-input-search"
                :value="searchIcon"
              />
              <button class="bby-icon-button bby-no-bg" @click="clearSearchBox"><BaseIconBby name="bbyDel" width="12" height="12" /></button>
            </div>
            <div class="bby-logo-icon-wrap">
              <div v-if="noContent" class="bby-logo-empty">
                Sorry, there are no local results for <i> {{searchIcon}}</i>
              </div>
              <div class="bby-logo-icon" v-for="(category, index) in googleIcons.categories" :key="index">
               <h5>{{ index }}</h5>

               <div class="bby-logo-icon-list-wrapper">
                 <div class="bby-logo-icon-single" v-for="(icon, index2) in googleIcons.categories[index]" :key="index2">
                   <a class="chooseSingleIcon" href="#" @click.prevent="chooseInternalIcon(icon.iconSet, icon.iconName)">
                     <svg width="35px" height="35px" viewBox="0 0 512 512" fill="#4e4e4e">
                       <use :xlink:href="icon.iconSpriteURL + '#' + icon.iconName" :id="icon.iconName"></use>
                     </svg>
                   </a>
                 </div>
               </div>
              </div>

              <div v-if="useSearch" class="bby-use-search" >
                <b>Use searchbox on top for searching the icon</b>
              </div>
              <div v-else-if="!useSearch && logoLicenseActive" class="bby-load-more">
                <button class="bby-button bby-button-small bby-bg-primaryIndigo" @click="loadMoreIcons()">Load more icons for '{{searchIcon}}'</button>
              </div>
              <div v-else-if="!useSearch && !logoLicenseActive" class="bby-load-more">
                <b style='color:red !important;'>Oh snap! You don't have an active license. You can fix it <a :href="buyLicenseLink">here</a></b>
              </div>

              <div v-if="moreIcons" class="bby-logo-icon-list">
                <div v-if="ifIconsSpriteIDs.length" class="bby-logo-icon-list-wrapper">
                    <div class="bby-logo-icon-single" v-for="(ifIconsSpriteID, index) in ifIconsSpriteIDs" :key="index">
                      <a class="chooseSingleIcon" href="#" @click.prevent="chooseIfIcon(ifIconsSpriteID)">
                        <svg width="35" height="35" viewBox="0 0 512 512" fill="#4e4e4e">
                           <use xmlns:xlink="http://www.w3.org/1999/xlink" :xlink:href="'#'+ifIconsSpriteID"></use>
                        </svg>
                      </a>
                    </div>
                </div>
                <div v-else-if="noRemoteContent" class="bby-logo-empty">
                  Sorry, there are no remote results for <i> {{searchIcon}}</i>
                </div>
                <div v-else class="bby-spiner-wrap">
                  <div class="bby-spinner"></div>
                </div>
              </div>

           </div>
         </div>
        </transition>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapState } from 'vuex';
import _ from 'lodash';
import {LambdaAPI} from "@/services/AppConfig.js"
import {WebsiteUrl} from "@/services/AppConfig.js"
import {WordpressConfig} from "@/services/AppConfig.js"
import TabsService from "@/services/TabsService.js";
import {AppVersion} from "@/services/AppConfig.js"


export default {
  data () {
    return {
      searchIcon: '',
      googleIcons: {},
      selectedIcon: '',
      toggleListShow: false,
      categoryEndPos: 3,
      noContent: false,
      noRemoteContent: false,
      ifIconsSprite:'',
      ifIconsSpriteIDs:[],
      moreIcons:false,
      useSearch: true,
      choosenIfIcon: '',
      buyLicenseLink: `${WebsiteUrl.WEBSITE_URL}/rd/buyLicense`,
      appVersion: AppVersion.APP_VERSION
    }
  },

  props: {
    firstComponentProps: Object,
    title: String
  },
  beforeMount(){
    let iconSelectedValue = JSON.parse(JSON.stringify(this.firstComponentProps.selected_value))
    this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: iconSelectedValue}])
  },
  created() {
    if (_.isEmpty(this.googleIconsState)){
      this.$store.dispatch("fetchIcons");
    }
  },
  computed:{
    ...mapState({
      googleIconsState: state => state.googleIcons,
      ifIconsState: state => state.ifIcons,
      logoLicenseActive: state => state.licenseActive
      })
  },
  watch: {
    googleIconsState(newValue, oldValue) {
      this.googleIcons = this.getIconsSubset(newValue)
    },
    searchIcon:
      _.debounce(function(val){
        var scrollElement = this.$el.getElementsByClassName('bby-f-dropdown')[0]
        scrollElement.scrollTop = 0
        this.ifIconsSprite = ''
        this.ifIconsSpriteIDs = []

          if (val){
            this.useSearch = false;
            let googleIconsFiltered = {}
            for (let category in this.googleIconsState.categories){
               for (let i=0; i < this.googleIconsState.categories[category].length; i++){
                 let iconTagFound = this.googleIconsState.categories[category][i].iconTags.filter(function (e){
                   return e.includes(val)
                 })
                 let iconNameFound = this.googleIconsState.categories[category][i].iconName.includes(val)
                 if (!_.isEmpty(iconTagFound) || iconNameFound){
                   googleIconsFiltered = _.assign({[category]:[]}, googleIconsFiltered)
                   googleIconsFiltered[category].push(this.googleIconsState.categories[category][i])
                 }
               }
            }
            if (_.isEmpty(googleIconsFiltered) && this.searchIcon != 0) {
              this.noContent = true
            }
            else {
              this.noContent = false
            }

            const googleIconsFilteredOrdered = {};
            Object.keys(googleIconsFiltered).sort().forEach(function(key) {
              googleIconsFilteredOrdered[key] = googleIconsFiltered[key];
            });
            this.googleIcons = {categories: googleIconsFilteredOrdered}
        }
        else {
          this.useSearch = true
          this.categoryEndPos = 3
          this.googleIcons = this.getIconsSubset(this.googleIconsState)
        }

    }, 250)
  },
  methods:{
    loadMoreIcons(){
      this.moreIcons = true
      this.noRemoteContent = false;
      this.ifIconsSprite = ''
      this.ifIconsSpriteIDs = []
      if (this.searchIcon.length < 30){
        this.fetchIfIcons(this.searchIcon)
    } else {
      this.noRemoteContent = true;
    }
    },
    fetchIfIcons(searchTerm){
      this.$nextTick(() => {
        var scrollElement = this.$el.getElementsByClassName('bby-f-dropdown')[0]
        var scrollHeight = scrollElement.scrollHeight
        var currentScrollPosition = scrollElement.scrollTop + scrollElement.clientHeight
        for (var y = currentScrollPosition; y <= scrollHeight; y += 1) {
          scrollElement.scrollTo({top: y, behavior: 'smooth'})
        }
    })
      axios.post(LambdaAPI.API_LOGO_ENDPOINT + 'icons/getdetails',
      {
        "search": searchTerm,
        "appVersion":this.appVersion
      },
      { headers:
        {
          "Content-Type": "application/json"
        }
      })
      .then(response =>{
        var ifIcons = response.data.message.Item.svgSprites;
        for (var i = 0; i < ifIcons.length; i++) {
          if (ifIcons[i].svgSpriteURL != '') {
            for (var z = 0; z < ifIcons[i].svgSpriteIDs.length; z++) {
              this.ifIconsSpriteIDs.push(ifIcons[i].svgSpriteIDs[z])
            }
            axios.get(ifIcons[i].svgSpriteURL)
            .then(response =>{
              this.ifIconsSprite = this.ifIconsSprite + response.data
            })
          }
          else{
            axios.post(LambdaAPI.API_LOGO_ENDPOINT + 'icons/getsprite',
            {
              "search": searchTerm,
              "svgSpriteID":""+ifIcons[i].svgID,
              "appVersion":this.appVersion
            },
            { headers:
              {
                "Content-Type": "application/json"
              }
            })
            .then(response =>{
              for (var t = 0; t < response.data.message.Item.svgSprites.svgSpriteIDs.length; t++) {
                this.ifIconsSpriteIDs.push(response.data.message.Item.svgSprites.svgSpriteIDs[t])
              }
              axios.get(response.data.message.Item.svgSprites.svgSpriteURL)
              .then(response =>{
                this.ifIconsSprite = this.ifIconsSprite + response.data
              })
            })
          }
        }
      })
      .catch(error =>{
        if (error.response.status == 404 || error.response.status == 400){
          this.noRemoteContent = true
        } else {
          this.noRemoteContent = false
        }
      })
    },
    handleScroll(){
      var scrollElement = this.$el.getElementsByClassName('bby-f-dropdown')[0]
      var scrollHeight = scrollElement.scrollHeight
      var currentScrollPosition = scrollElement.scrollTop + scrollElement.clientHeight
      var scrollRatio = (currentScrollPosition / scrollHeight) * 100
      if (scrollRatio.toFixed(0) > 80){
        this.loadMore()
      }
    },
    loadMore(){
      this.categoryEndPos = this.categoryEndPos + 3
      if (!this.searchIcon){
        this.googleIcons = this.getIconsSubset(this.googleIconsState)
      }
    },
    getIconsSubset(newValue){
      let categoriesList = Object.keys(newValue.categories)
      categoriesList = categoriesList.slice(0,this.categoryEndPos)
      let slicedCategories = {'categories':{}}
      for (let i=0; i < categoriesList.length; i++){
        slicedCategories.categories = _.assign(slicedCategories.categories, { [categoriesList[i]]:newValue.categories[categoriesList[i]]} )
      }
      return slicedCategories
    },
    chooseIcon(event, icon){
      icon = {...icon, iconClass:event.target.className};
      var clearFontIcon = _.omit(this.$store.state.values.iconFunctionalLogoIcons, Object.keys(icon));
      var clearFontIconString = Object.keys(clearFontIcon)
      clearFontIconString.forEach( item => {
        icon = {...icon, [item]: ""}
      });
      delete icon.iconTags;
      this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: icon}]);
    },
    chooseInternalIcon(iconSet, iconName){
      axios.get(WordpressConfig.WP_MAIN_PAGE_URL + `&action=getIconSVG&iconSet=${iconSet}&iconName=${iconName}`,
      {
        headers: TabsService.getAxiosHeadersConfig()
      })
      .then((response) => {
        let iconSvgCode = response.data.message
        let icon = {
          iconSvgCode: iconSvgCode
        }
        this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: icon}]);
      })
    },
    chooseIfIcon(iconId, event){
      var rawIconCode = document.getElementById(iconId).innerHTML.trim();
      var iconCode = rawIconCode
      var icon = {...icon, iconSvgCode: iconCode};
      this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: icon}]);
    },
    searchIconsInput(event){
      this.searchIcon = event.target.value;
      this.moreIcons = false
    },
    clearSearchBox(){
      this.searchIcon = '';
      this.noContent = false;
      this.noRemoteContent = false;
      this.moreIcons = false
    },
    onClose () {
      this.toggleListShow = false
    },
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
      el.addEventListener('scroll', this.handleScroll)
    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      this.categoryEndPos = 3
      this.googleIcons = this.getIconsSubset(this.googleIconsState)
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      }, 200);
      el.removeEventListener('scroll', this.handleScroll)
    }
  }
}

</script>

<style lang="scss" scoped>

.bby-logo-icon-list{
    margin-top: 1.5rem
}

.bby-use-search{
  position: sticky;
  bottom: 0 ;
  left: 0;
  background: #fff;
  padding: 5px;
}


.bby-logo-icon-single {
  padding: 10px;
  cursor: pointer;
}

</style>
