import axios from "axios";
import {WordpressConfig} from "@/services/AppConfig.js"
import {LambdaAPI} from "@/services/AppConfig.js"
import {AppVersion} from "@/services/AppConfig.js"


const axiosHeadersConfig = {
    "Accept": "application/json",
    "Content-Type": "application/json",
    "X-BuilderBoy": "QnVpbGRlckJveQ=="
}

const apiClient = axios.create({
  baseURL: WordpressConfig.JSON_HOST_ADDRESS,
  withCredentials: false,
  headers: axiosHeadersConfig
});

export default {
  getTabContent() {
    return apiClient.get(WordpressConfig.JSON_HOST_PATH);
  },
  getFonts(){
    return apiClient.get(WordpressConfig.JSON_GOOGLE_FONTS);
  },
  getIcons(){
    return apiClient.get(WordpressConfig.JSON_GOOGLE_ICONS);
  },
  saveJson(){
    return apiClient.get(WordpressConfig.WP_MAIN_PAGE_URL + '&action=internalJSONupdate&force=true')
  },
  saveIcon(){
    return apiClient.get(WordpressConfig.WP_MAIN_PAGE_URL + '&action=internalSaveIcons&force=true')
  },
  saveGoogleFonts(){
    return apiClient.get(WordpressConfig.WP_MAIN_PAGE_URL + '&action=internalSaveGoogleFonts&force=true')
  },
  saveAssets(){
    return apiClient.get(WordpressConfig.WP_MAIN_PAGE_URL + '&action=internalSavePanelAssets&force=true')
  },
  checkForUpdate(){
    return apiClient.get(LambdaAPI.API_LOGO_ENDPOINT + 'version/latest')
  },
  checkLicenseActive(license){
      return apiClient.post(LambdaAPI.API_LOGO_ENDPOINT + 'license/validate',
      {
        // "licenseCode":"281277bb-3828-4032-8793-000000000001",
        "licenseCode": license,
        "email":"test@remote.com",
        "appVersion":AppVersion.APP_VERSION
      })
  },



  contentValues(data,newValues){
    var newData = [];
    for (var i in data) {
      if (typeof data[i] === 'object') {
        this.contentValues(data[i],newValues)
      }
      for (var y in newValues) {
        if (data[i].name === y) {
          data[i].selected_value = newValues[y]
        }
      }
    }
    newData = data;
    return newData;
  },

  getAxiosHeadersConfig(){
    return axiosHeadersConfig
  }

};
