<template lang="html">
  <div class="bby-box-component">

    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>
    
    <transition
      name="expand"
      @enter="enterError"
      @after-enter="afterEnterError"
      @leave="leaveError"
    >
      <div class="bby-range-slider-error bby-error" v-show="showError">
        <p>Ooops! This option is not available in free plan.</p>
      </div>
    </transition>

    <div v-for="(firstComponentProp, index) in firstComponentProps.items"  :key="index" :class="{inputError: showError}">
      <label :for="firstComponentProps.name">
        <div class="switch-box-wrapper">
          <div class="switch-box" :class="styleCheckbox">
            <div class="switch-box-circle"></div>
          </div>
        </div>
      </label>

      <input
        hidden
        type="checkbox"
        :id="firstComponentProps.name"
        :name="firstComponentProps.name"
        :checked="checkedInputValue"
        @change="updateCheckbox"/>


        <div v-if="firstComponentProp.options != 0">
          <transition
          name="expand"
          @enter="enter"
          @after-enter="afterEnter"
          @leave="leave"
          >
            <div v-if="checkedValue && !showError">
              <div class="bby-box-component" v-for="(option, index) in firstComponentProp.options" :key="index">
                <!-- <h5>{{option.propertyName}}</h5> -->
                <div v-for="(secondComponents, index) in option" :key="index">
                  <!-- {{secondComponents}} -->
                  <component
                    :is="secondComponents.type"
                    :firstComponentProps="secondComponents"
                    :title="option.propertyName"
                  ></component>
                </div>
              </div>
            </div>
          </transition>
        </div>

    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  data () {
    return {
      checkedValue: '',
      checkedInputValue: '',
      styleCheckbox: '',
      showError: false,
      licenseActive: null,
      licenseRequired: (this.firstComponentProps.licenseRequired.toLowerCase() === 'true')
    }
  },
  props: {
    firstComponentProps: Object,
    title: String
  },
  created(){
    this.licenseActive = this.$store.state.licenseActive
    if (this.$store.state.values[this.firstComponentProps.name] == null) {
      this.$store.commit("UPDATE_COMPONENT_VALUES", [{name: this.firstComponentProps.name, value: ""+this.firstComponentProps.selected_value}])
    }
    else {
      this.checkedValue = this.checkboxState == "true"
    }
  },
  computed:{
    ...mapState({
      checkboxState (state){
        return state.values[this.firstComponentProps.name]
      },
      licenseActiveState: state => state.licenseActive
    })
  },
  watch:{
    licenseActive(val){
      this.setError()
    },
    showError(val){
      this.$store.commit("UPDATE_COMPONENT_ERRORS", [{value: val, name: this.firstComponentProps.name}]);
    },
    licenseActiveState(val){
      this.licenseActive = val
    },
    checkboxState(val){
      if (val === 'true') {
        this.checkedValue = true
      }
      else {
        this.checkedValue = false
      }
    },
    checkedValue(val){
      if (val) {
        this.checkedInputValue = 'checked'
        this.styleCheckbox = 'switchBoxOn'
        this.setError()
      }
      else {
        this.checkedInputValue = ''
        this.styleCheckbox = 'switchBoxOff'
        this.setError()
      }
    }
  },
  methods:{
    setError(){
      if (this.licenseActive == null){
        return;
      }
        if (!this.licenseActive && this.licenseRequired && this.checkedValue){
          this.showError = true
        } else {
          this.showError = false
        }
    },
    enterError(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
       el.addEventListener('scroll', this.handleScroll)

    },
    afterEnterError(el) {
      el.style.height = 'auto';
    },
    leaveError(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    },
    updateCheckbox(event){
      this.$store.commit("UPDATE_COMPONENT_VALUES", [{name: event.target.name, value: ""+event.target.checked}])
    },
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    }
  },
}
</script>

<style lang="css" scoped >

.expand-enter-active, .expand-leave-active {
  transition: height .3s ease-in-out;
  overflow: hidden;
}

.switch-enter-active {
  transition: all .3s ease;
}
.switch-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.switch-enter, .switch-leave-to {
  transform: translateX(10px);
  opacity: 0;
}

</style>
