<template lang="html">
  <div class="bby-component">
    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>

    <color-picker
        theme="light"
        :color="getColor()"
        :sucker-hide="true"
        @changeColor="changeColor($event, firstComponentProps.name)"
      />
  </div>
</template>

<script>
import colorPicker from '@caohenghu/vue-colorpicker';
import _ from 'lodash';
export default {
  data() {
    return {
      color: '',
      suckerCanvas: null,
      isSucking: false
    }
  },
  components: {
    colorPicker
  },
  methods: {
    updateVuex:
      _.throttle(function(color, name){
        this.$store.commit("UPDATE_COLOR_PICKER", [{rgba: color.rgba, hex: color.hex, name: name}]);
      }, 10),

    changeColor(color, name){
      const { r, g, b, a } = color.rgba;
      this.color = `rgba(${r}, ${g}, ${b}, ${a})`
      this.updateVuex(color, name)
    },
    getColor(){
      const colorPickerName = this.firstComponentProps.name
      var colorPickerValuesVuex = this.$store.state.values[colorPickerName]
      var { r, g, b, a } = {r: 255, g: 255, b: 255, a:1}

      if (!colorPickerValuesVuex){
        ({ r, g, b, a } = this.firstComponentProps.selected_value[colorPickerName+'_rgba']);
      } else {
        ({ r, g, b, a } = this.$store.state.values[colorPickerName][colorPickerName+'_rgba']);
      }
      return `rgba(${r}, ${g}, ${b}, ${a})`
   }
  },
  props: {
    firstComponentProps: Object,
    propertyName: Object,
    title: String
  }
};
</script>

