<template lang="html">
  <div class="bby-box-component">

    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>

    <div class="bby-component bby-select">
      <select @change="updateSelect" :name="firstComponentProps.name" class="bby-stroke-primaryMediumGray">
        <option
          v-for="(fontVariants, index) in fontsVariants"
          :key="index"
          :selected="selectedDropdown(fontVariants)"
          :value="fontVariants">
            {{fontVariants}}
        </option>
      </select>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import _ from 'lodash';

export default {
  data() {
    return {
      selectedFont:'',
      fontsVariants: {}
    }
  },
  props: {
    firstComponentProps: Object,
    title: String
  },
  beforeMount(){
    if (this.$store.state.values[this.firstComponentProps.name] == null) {
      this.$store.commit("UPDATE_LOGO_VALUES", [{name: this.firstComponentProps.name, value: this.firstComponentProps.selected_value}])
    } else {
      this.selectedFont = this.fontFamilyState
    }
  },
  watch:{
    googleFonts(newval, oldval){
      this.selectedFont = this.fontFamilyState
    },
    fontFamilyState(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        this.selectedFont = newval
        this.updateSelect({target: {value: 'regular', name: this.firstComponentProps.name}})
      }
    },
    selectedFont(newval, oldval){
      if (!_.isEqual(newval, oldval)){
        for (var i in this.googleFonts) {
          if (this.selectedFont === this.googleFonts[i].family) {
            this.fontsVariants = this.googleFonts[i].variants
          }
        }
      }
    }
  },
  computed:{
    ...mapState({
      googleFonts: state => state.googleFonts,
      fontFamilyState: state => state.values.textFontFamilyDropDownList,
      fontWeightState: state => state.values.textFontWeightDropDownList
    }),
  },
  methods:{
    updateSelect(event){
      var logoFontStyle
      var logoFontUrl
      var logoFontWeight
      var logoFontEvent = event.target.value
      var logoFont = {}

      logoFontStyle = logoFontEvent.match("[a-zA-Z]*$")
      logoFontWeight = logoFontEvent.match("^[0-9]{3}")

      if (logoFontStyle[0] == 'regular'){
        logoFontWeight = []
        logoFontStyle[0] = 'normal'
        logoFontWeight[0] = '400'
      }

      if (logoFontStyle[0] == 'italic' && logoFontWeight == null){
        logoFontWeight = []
        logoFontWeight[0] = '400'
      }

      if (logoFontStyle == ''){
        logoFontStyle = []
        logoFontStyle[0] = 'normal'
      }

      for (let i in this.googleFonts){
        if (this.googleFonts[i].family == this.fontFamilyState){
          logoFontUrl = this.googleFonts[i].files[logoFontEvent]
          if (logoFontUrl == null){
            logoFontUrl = this.googleFonts[i].files['300']
            logoFontWeight[0] = '300'
            logoFontStyle[0] = 'normal'
          }
        }
      }

      logoFont = {logoFontStyle: logoFontStyle[0], logoFontUrl: logoFontUrl, logoFontWeight: logoFontWeight[0]}

      this.$store.commit("UPDATE_LOGO_VALUES", [{name: event.target.name, value: logoFont}])
    },
    selectedDropdown(variant){
      var logoFont = this.fontWeightState.logoFontWeight
      var logoStyle = this.fontWeightState.logoFontStyle

      if (logoFont == '400'){
        logoFont = 'regular'
      }

      if (logoStyle == 'italic'){
        logoFont = logoFont + logoStyle
      }

      if (logoFont == variant){
        return 'selected'
      }
      else {
        return ''
      }

    }
  }
};
</script>

