<template lang="html">
  <div class="bby-style-flex bby-column">
    <div class="header container-fluid">
      <header class="row">
        <div class="bby-header col-sm bby-style-flex bby-row bby-space-between bby-center">
          <div>
            <a href="#"><BaseIconBby name="bbyLogoCreatorWhite" height="50" width="200" /></a>
          </div>
          <div class="bby-header-navigation">
            <div class="bby-header-hamburger">
              <button type="button" class="bby-icon-button bby-no-bg" name="button" @click="openMenu"><BaseIconBby name="bbyMenu" width="32" height="32" fill="#fff" /></button>
            </div>
            <transition name="menuAnimation">
              <div v-if="showMenu" class="bby-header-menu" id="bbyMenu">
                <button type="button" class="bby-header-menu-close bby-icon-button bby-no-bg" name="button" @click="closeMenu"><BaseIconBby name="bbyDel" width="32" height="32" fill="#fff" /></button>
                <nav class="bby-header-menu-nav">
                  <ul class="bby-header-menu-links bby-style-flex bby-column bby-space-evenly">
                    <li><a :href="`${WebsiteUrl}/rd/documentation`">Documentation</a></li>
                    <li v-if="!licenseActiveState">
                      <span>
                        <BaseHint iconHintName="bbyInfo" fill="#ffffff" hintValue="This option is not available in free version."></BaseHint>
                      </span>
                      <a href="#">Advanced settings</a>
                    </li>
                    <li v-else><a href="?page=bby_admin_logo_builder_advanced_settings">Advanced settings</a>
                    <li><a :href="`${WebsiteUrl}/rd/contact`">Contact</a></li>
                  </ul>
                </nav>
                <button v-if="!licenseActiveState" @click="buyNow()" type="button" name="button" class="bby-button bby-button-medium  bby-bg-primaryAqua bby-color-primaryWhite">BUY LICENSE</button>
                <button v-if="showSave" type="button" name="button" class="bby-button bby-button-medium  bby-bg-primaryIndigo bby-color-primaryWhite" @click="updateValues()">SAVE CHANGES</button>
                <button type="button" name="button" class="bby-button-stroke bby-button-small bby-stroke-primaryWhite" @click="executeButtonAction()">Reset to defaults</button>
              </div>
            </transition>
          </div>

        </div>
      </header>
    </div>
    <div v-for="(headerNotification, index) in headerNotifications" :key="index" class="bby-subheader-notifications">
      <transition
      name="expand"
      @enter="enter"
      @after-enter="afterEnter"
      @leave="leave"
      >
      <div :keys="index" v-show="headerNotification.text" class="bby-subheader-notifications-single">
        <div class="bby-subheader-notifications-inner bby-style-flex bby-row bby-space-between bby-center">
          <div v-html="headerNotification.text"></div>
          <button type="button" class="bby-icon-button bby-no-bg" name="button" @click="closeNotification(index)"><BaseIconBby name="bbyDel" width="12" height="12" fill="#000" /></button>
        </div>
      </div>
      </transition>
    </div>
    <BaseModal @clicked="onClickChild" :modalDisplay="addLicense" />
  </div>
</template>

<script>
import axios from "axios";
import TabsService from "@/services/TabsService.js";
import sendNotification from "@/services/NotificationService.js";
import {WordpressConfig} from "@/services/AppConfig.js"
import { mapState } from "vuex";
import {WebsiteUrl} from "@/services/AppConfig.js"


export default {
  data () {
    return {
      showMenu: false,
      licenseInfo: '',
      licenseActive: '',
      headerNotifications:[],
      addLicense: false,
      formError: false,
      WebsiteUrl: WebsiteUrl.WEBSITE_URL,
      firstInstall: null
    }
  },
  props:{
    headerValues: Object
  },
  updated(){
    if (document.getElementById('licenseModal')){
      var elem = document.getElementById('licenseModal').onclick = this.licenseModal
    }
  },
  watch: {
    headerValues(val){
      if (val.selected_value.init.toLowerCase() == 'true'){
        this.firstInstall = true
      } else if (val.selected_value.init.toLowerCase() == 'false'){
        this.firstInstall = false
      }

      if (!this.firstInstall) {
        this.licenseStatusInstall(val.selected_value.license)
        this.showSave = true
      }
      else {
        this.showSave = false
      }
    },
    licenseValuesState(val){
      var firstInstall
      if (val.init.toLowerCase() == 'true'){
        firstInstall = true
      } else if (val.init.toLowerCase() == 'false'){
        firstInstall = false
      }

      if (firstInstall) {
        this.licenseStatusInstall(val.license)
        this.showSave = false
      }
      else {
        this.showSave = true
      }
    },
    licenseActiveState(val){
      this.licenseStatusInstall(val)
    },
    updateAvailable(val){
      if (val) {
        var text = `UPDATE IS AVAILABLE, YOU CAN DOWNLOAD IT HERE: <a href="${WebsiteUrl.WEBSITE_URL}/rd/logobuilder/download"><b>LATEST VERSION</b></a>`
        this.headerNotifications = {...this.headerNotifications, update:{text}}
      }
    },
    showMenu(val){
        var notificationBox = document.getElementsByClassName('vue-notification-group')[0]
        notificationBox.classList.toggle('menuIsOpen', val)
    },
  },
  computed: {
    ...mapState({
      licenseActiveState: state => state.licenseActive,
      updateAvailable: state => state.updateAvailable,
      licenseValuesState: state => state.values.licenseVerification,
      licenseCodeState: state => state.contentTabs.adminSettings.selected_value,
      componentErrorsState: state => state.componentErrors
    })
  },
  methods: {
    buyNow(){
      window.open(`${WebsiteUrl.WEBSITE_URL}/rd/logobuilder/buyLicense`,"_self")
    },
    updateValues(){
      for (let i in this.componentErrorsState){
        this.formError = this.componentErrorsState[i]
        if (this.formError) break
      }

      if (this.formError) {
        sendNotification('Ooops some fields are incorrect!', 'errors')
      } else {
        this.$store.commit('replaceValues')
      }
    },
    onClickChild(){
      this.addLicense = false
    },
    licenseModal(){
      this.addLicense = true
    },
    licenseStatusInstall(license){
      if (license != '') {
        var text = '<span class="active">license status:<b> ACTIVE</b></span>'
        this.headerNotifications = {...this.headerNotifications, licenseStat:{text}}
        setTimeout(() => {
          this.headerNotifications.licenseStat = ''
        }, 3000)
      }
      else {
        if (!this.firstInstall){
          var text = '<span class="inactive">license status:<b> INACTIVE</b>, you can activate here: <button class="bby-icon-button bby-no-bg" id="licenseModal"><b>activate</b></button></span>'
          this.headerNotifications = {...this.headerNotifications, licenseStat:{text}}
        }
      }
    },
    closeNotification(index){
      this.headerNotifications[index].text = ''
    },
    enter(el) {
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    },
    openMenu(){
      this.showMenu = true
    },
    closeMenu(){
      this.showMenu = false
    },
    executeButtonAction(){
        axios.get(WordpressConfig.WP_MAIN_PAGE_URL + '&action=internalJSONupdate&force=true',
        {
          headers: TabsService.getAxiosHeadersConfig()
        })
      .then(function (response) {
        let status = response.data
        let notificationType = ''
        if (status.error){
          notificationType = 'error'
        } else {
          notificationType = 'success'
        }
        sendNotification(status.message, notificationType)
          return axios.get(WordpressConfig.WP_MAIN_PAGE_URL + '&action=internalSaveIcons&force=true',
        {
          headers: TabsService.getAxiosHeadersConfig()
        })
      })
      .then(function (response) {
        let status = response.data
        let notificationType = ''
        if (status.error){
          notificationType = 'error'
        } else {
          notificationType = 'success'
        }
        sendNotification(status.message, notificationType)
        return axios.get(WordpressConfig.WP_MAIN_PAGE_URL + '&action=internalSaveGoogleFonts&force=true',
        {
          headers: TabsService.getAxiosHeadersConfig()
        })
      })
      .then(function (response) {
        let status = response.data
        let notificationType = ''
        if (status.error){
          notificationType = 'error'
        } else {
          notificationType = 'success'
        }
        sendNotification(status.message, notificationType)
        return axios.get(WordpressConfig.WP_MAIN_PAGE_URL + '&action=internalSavePanelAssets&force=true',
        {
          headers: TabsService.getAxiosHeadersConfig()
        })
      })
      .then(function (response) {
        let status = response.data
        let notificationType = ''
        if (status.error){
          notificationType = 'error'
        } else {
          notificationType = 'success'
        }
        sendNotification(status.message, notificationType)
        setTimeout( () => {location.reload()}, 1500);
      })
      .catch(function (error) {
        let status = error.response.data
        let notificationType = ''
        if (status.error){
          notificationType = 'error'
        } else {
          notificationType = 'success'
        }
        sendNotification(status.message, notificationType)
      });
    }
  }
}
</script>

<style lang="css" scoped>

.bby-header-menu-close{
  margin-top: 25px;
}

</style>
