import Vue from "vue";
import Vuex from "vuex";
import TabsService from "@/services/TabsService.js";
import axios from "axios";
import _ from 'lodash';
import sendNotification from "@/services/NotificationService.js";
import {WordpressConfig} from "@/services/AppConfig.js"


Vue.use(Vuex);


export default new Vuex.Store({
  strict: true,
  state: {
    contentTabs: [],
    googleIcons: {},
    googleFonts: [],
    selectedContentTabsId: "011",
    componentErrors:{},
    values:{},
    licenseActive: null,
    updateAvailable: false
  },
  mutations: {
    SET_COMPONENT(state, contentTabs) {
      state.contentTabs = contentTabs;
    },
    SET_ICONS(state, googleIcons){
      state.googleIcons = googleIcons
    },
    SET_FONTS(state, googleFonts){
      state.googleFonts = googleFonts.items
    },
    LICENSE_STATUS(state,value){
      state.licenseActive = value.active
    },
    SET_IF_ICONS(state, ifIcons){
      state.ifIcons = ifIcons.message
    },
    TOGGLE_TAB(state, value) {
      state.selectedContentTabsId = value;
    },
    UPDATE_COMPONENT_VALUES(state, value){
      for (var i = 0; i < value.length; i++) {
        state.values = Object.assign({}, state.values, { [value[i].name]: value[i].value })
      }
    },
    UPDATE_COMPONENT_ERRORS(state, value){
      for (var i = 0; i < value.length; i++) {
        state.componentErrors = Object.assign({}, state.componentErrors, { [value[i].name]: value[i].value })
      }
    },
    UPDATE_SELECT_WEIGHT(state, value){
      for (var i = 0; i < value.length; i++) {
        state.values = Object.assign({}, state.values, { [value[i].name]: value[i].value })
      }
    },
    UPDATE_SELECT_FONT_LOGO_URL(state, value){
      for (var i = 0; i < value.length; i++) {
        state.values = Object.assign({}, state.values, { textLogoFontFaceUrl: value[i].value })
      }
    },
    UPDATE_COLOR_PICKER(state, value){
      for (var i = 0; i < value.length; i++) {
        state.values = Object.assign({}, state.values, {
          [value[i].name]:{
            [value[i].name+'_hex']: value[i].hex,
            [value[i].name+'_rgba']: value[i].rgba
          }
        })
      }
    },
    UPDATE_ICON_CHECKBOX(state, value){
      for (var i = 0; i < value.length; i++) {
        state.values = _.merge(state.values, { [value[i].name]: value[i].value })
      }
    },
    UPDATE_LOGO_VALUES(state, value){
      for (var i = 0; i < value.length; i++) {
        state.values[value[i].name] = {...state.values[value[i].name], ...value[i].value}
      }
    },
    UPDATE_LOGO_TEXT_FONT_FAMILY(state, value){
      for (var i = 0; i < value.length; i++) {
        state.values = Object.assign({}, state.values, { [value[i].name]: value[i].value })
      }
    },
    UPDATE_INSTALLATION(state, value){
      for (var i = 0; i < value.length; i++) {
        // state.contentTabs.adminSettings.selected_value = _.merge(state.contentTabs.adminSettings.selected_value, value[i])
        // state.values = Object.assign({}, state.values, { [value[i].name]: value[i].value })
        state.values[value[i].name] = _.merge(state.values[value[i].name], value[i].value)
        // state.values = Object.assign({}, state.values, { [value[i].name]: value[i].value })
      }
    },
    UPDATE_UPDATE_AVAILABLE(state, value){
        state.updateAvailable = value
    },
    replaceValues(state){
      if (state.values) {
        var globalSettings = TabsService.contentValues(state.contentTabs, state.values);
        // state.newValues = globalSettings;
        axios.post(WordpressConfig.WP_MAIN_PAGE_URL + '&action=postJSONvalues',
        globalSettings, { headers: TabsService.getAxiosHeadersConfig() })
        .then(function (response) {
          let status = response.data
          let notificationType = ''
          if (status.error){
            notificationType = 'errors'
          } else {
            notificationType = 'success'
          }
          sendNotification(status.message, notificationType)
        })
        .catch(function (error) {
          let status = error.response.data
          let notificationType = ''
          if (status.error){
            notificationType = 'errors'
          } else {
            notificationType = 'success'
          }
          sendNotification(status.message, notificationType)
        });
      }
    }
  },
  actions: {
    logoActions({ commit },values){
      commit("CREATE_LOGO", values);
    },
    fetchTabsContent({ commit, dispatch }) {
      TabsService.getTabContent()
      .then(response => {
        commit("SET_COMPONENT", response.data);
      })
      .catch(error => {
        console.log('Error getting JSON values: ', error.response)
        let status = error.response.data
        let notificationType = ''
        if (status.error){
          notificationType = 'error'
        } else {
          notificationType = 'success'
        }
        sendNotification('Something went wrong! Please contact our support team.', notificationType)
      });
    },
    fetchFonts({ commit }){
      TabsService.getFonts()
      .then(response => {
        let googleFonts = {
          kind: "webfonts#webfontList",
          items: []
        }
        googleFonts.items = response.data.items.map((font) => {
          for (let variantFile in font.files){
            font.files[variantFile] = font.files[variantFile].replace(/http\:/g, '')
          }
          return font
        });
        commit("SET_FONTS", googleFonts);
      })
      .catch(error => {
        console.log("There was an error:", error.response);
      });
    },
    fetchIcons({ commit }){
      TabsService.getIcons()
      .then(response => {
        commit("SET_ICONS", response.data);
      })
      .catch(error => {
        console.log("There was an error:", error.response);
      });
    },
    fetchCheckForUpdate({ commit }){
      TabsService.checkForUpdate()
      .then(response => {
        let installedAppInfo = this.state.contentTabs.appInfo
        let remoteAppInfo = response.data

        let check = function isNewerVersion (oldVer, newVer) {
          const oldParts = oldVer.split('.')
          const newParts = newVer.split('.')
          for (var i = 0; i < newParts.length; i++) {
            const a = ~~newParts[i] // parse int
            const b = ~~oldParts[i] // parse int
            if (a > b) return true
            if (a < b) return false
          }
          return false
        }
        let updateAvailable = check(installedAppInfo.version, remoteAppInfo.currentVersion)
        if (updateAvailable){
          console.log('New version for Logo BuilderBoy is available!')
          commit("UPDATE_UPDATE_AVAILABLE", updateAvailable);

        }
      })
      .catch(error => {
        console.log("There was an error:", error.response);
      });
    }
  }
}
);
