<template>
  <div id="app">
      <TabsHeader :headerValues="contentTabs.adminSettings" />
      <TabsInstall v-if="firstInstall"/>
      <TabsSection v-else/>
      <TabsFooter v-if="!firstInstall"/>
      <notifications
        group="bby-notification"
        position="center"
        classes="bby-notification"
        :duration="1500"
        :speed="750" />
  </div>
</template>

<script>
import { mapState } from "vuex";
import TabsHeader from "@/components/TabsHeader.vue";
import TabsFooter from "@/components/TabsFooter.vue";
import TabsSection from "@/views/TabsSection.vue";
import TabsService from "@/services/TabsService.js";
import TabsInstall from "@/views/TabsInstall.vue";

export default {
  components: {
    TabsInstall,
    TabsHeader,
    TabsFooter,
    TabsSection
  },
  created() {
    this.$store.dispatch("fetchTabsContent");
  },
  data() {
    return {
      firstInstall: false
    };
  },
  computed: {
    ...mapState({
      contentTabs: state => state.contentTabs,
      licenseActive: state => state.licenseActive
    }),
  },
  methods: {
    encodeHtmlChars(str) {
      var i = str.length,
      result = [];
      while (i--) {
        var charCode = str[i].charCodeAt();
        if (charCode < 48 || (charCode > 57 && charCode < 65) || (charCode > 90 && charCode < 97) || charCode > 123) {
          result[i] = '&#'+charCode+';';
        } else {
          result[i] = str[i];
        }
      }
      return result.join('');
    },
  },
  watch: {
    'contentTabs.adminSettings.selected_value.init': function (val){
      if (val.toLowerCase() == 'true'){
        this.firstInstall = true
      } else if (val.toLowerCase() == 'false'){
        this.firstInstall = false
      }
    },
    'contentTabs.appInfo.version': function (val){
      this.$store.dispatch("fetchCheckForUpdate");
    },
    'contentTabs.adminSettings.selected_value.license': function (val){
      if (!!val){
        var encodeLicenseValue = this.encodeHtmlChars(val)
        TabsService.checkLicenseActive(encodeLicenseValue)
        .then(response => {
            if (response.status == '200') {
                this.$store.commit('LICENSE_STATUS', {active: true})
            }
        })
        .catch(error =>{
          if (error.response.data.error) {
              this.$store.commit('LICENSE_STATUS', {active: false})
          }
        })
      } else {
        this.$store.commit('LICENSE_STATUS', {active: false})
      }
    },
  }
};
</script>

