<template lang="html">
  <div class="bby-box-component">

    <BaseComponentTitle :title="title" :hintObjectProps="firstComponentProps"/>
    
    <link v-if="fontFamilyList" :href="'https://fonts.googleapis.com/css?family='+fontFamilyList" rel="stylesheet">
    <div class="bby-component bby-dropdown">
      <div @click="toggleListShow = !toggleListShow" ref="toggle" class="bby-f-dropdown-input bby-stroke-primaryMediumGray">
        <input
          type="text"
          readonly
          :value="searchFont"
          class="bby-functional-input"
        >
        <span><BaseIconBby name="bbyDown" width="12" height="12" /></span>
      </div>
      <transition
      name="expand"
      @enter="enter"
      @after-enter="afterEnter"
      @leave="leave"
      >
        <div class="bby-f-dropdown" v-if="toggleListShow" v-closable="{ exclude: ['toggle'], handler: 'onClose' }">
          <div class="bby-f-dropdown-input-search">
            <input
              type="text"
              @input="searchFontInput"
              :value ="searchFontValue"
              placeholder="Start typing to search..."
              class="bby-f-dropdown-input-search"
            >
            <button class="bby-icon-button bby-no-bg" @click="clearSearchBox"><BaseIconBby name="bbyDel" width="12" height="12" /></button>
          </div>
          <div class="bby-logo-font-wrap">
            <div v-if="noContent" class="bby-logo-empty">
              Sorry, there are no result for <i> {{searchFontValue}}</i>
            </div>
            <div class="bby-logo-font-single" v-for="(font, index) in googleFilteredFonts" :key="index">
              <a :style="'font-family:'+ font.family + ',' + font.category"
                href="#"
                @click.prevent="selectFont(font.family,firstComponentProps.name)">
                  {{font.family}}
              </a>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import _ from 'lodash';
export default {
  data() {
    return {
      toggleListShow: false,
      searchFont: '',
      searchFontValue: '',
      googleFilteredFonts:[],
      fontEndPos: 50,
      noContent: false
    }
  },
  props: {
    firstComponentProps: Object,
    title: String
  },
  beforeMount(){
    if (this.$store.state.values[this.firstComponentProps.name] == null) {
      this.$store.commit("UPDATE_LOGO_TEXT_FONT_FAMILY", [{name: this.firstComponentProps.name, value: this.firstComponentProps.selected_value}])
    } else {
      if (this.fontFamilyState != null) {
        this.searchFont = this.fontFamilyState
        this.googleFilteredFonts = this.getFontsSubset(this.googleFontsState)
      }
      else {
        this.searchFont = this.firstComponentProps.selected_value
        this.googleFilteredFonts = this.getFontsSubset(this.googleFontsState)
      }
    }
  },
  created() {
    if (_.isEmpty(this.googleFontsState)){
    this.$store.dispatch("fetchFonts");
    }
  },
  watch:{
    searchFontValue:
      _.debounce(function(val){
        var scrollElement = this.$el.getElementsByClassName('bby-f-dropdown')[0]
        scrollElement.scrollTop = 0
        if (val){
          this.googleFilteredFonts = []
          for (var i in this.googleFontsState) {
            if (this.googleFontsState[i].family.toLowerCase().includes(val)) {
              this.googleFilteredFonts.push(this.googleFontsState[i])
            }
          }
          if (_.isEmpty(this.googleFilteredFonts) && this.searchFontValue != 0) {
            this.noContent = true
          }
          else {
            this.noContent = false
          }
        }
        else {
          this.fontEndPos = 50
          this.googleFilteredFonts = this.getFontsSubset(this.googleFontsState)
        }
      }, 250),
    googleFontsState(val){
      if (this.fontFamilyState != null) {
        this.searchFont = this.fontFamilyState
        this.googleFilteredFonts = this.getFontsSubset(this.googleFontsState)
      }
      else {
        this.searchFont = this.firstComponentProps.selected_value
        this.googleFilteredFonts = this.getFontsSubset(this.googleFontsState)
      }
    }
  },
  computed:{
    fontFamilyList(){
      var list = ''
      for (var i in this.googleFontsState){
        list = list + this.googleFontsState[i].family + "|"
      }
      // Remove last | character, which is needless
      list = list.slice(0, -1);
      return list
    },
    ...mapState({
      googleFontsState: state => state.googleFonts,
      fontFamilyState: state => state.values.textFontFamilyDropDownList,
      fontWeightState: state => state.values.textFontWeightDropDownList,
    })
  },
  methods:{
    clearSearchBox(){
      this.searchFontValue = '';
      this.noContent = false;
    },
    searchFontInput(event){
      this.searchFontValue = event.target.value
    },
    onClose () {
      this.toggleListShow = false
    },
    selectFont(fontFamily){
      this.searchFont = fontFamily
      this.$store.commit("UPDATE_LOGO_TEXT_FONT_FAMILY", [{name: this.firstComponentProps.name, value: fontFamily}])
    },
    enter(el) {
      this.googleFilteredFonts = this.getFontsSubset(this.googleFontsState)
      this.searchIcon = '';
      el.style.height = 'auto';
      const height = getComputedStyle(el).height;
      el.style.height = 0;
      getComputedStyle(el);
      setTimeout(() => {
         el.style.height = height;
       });
       el.addEventListener('scroll', this.handleScroll)

    },
    afterEnter(el) {
      el.style.height = 'auto';
    },
    leave(el) {
      el.style.height = getComputedStyle(el).height;
      getComputedStyle(el);
      setTimeout(() => {
        el.style.height = 0;
      });
    },
    getFontsSubset(newValue){
      let fontList = newValue.slice(0,this.fontEndPos)
      let slicedFonts = []
      for (let i=0; i < fontList.length; i++){
        slicedFonts[i] = newValue[i]
      }
      return slicedFonts
    },
    handleScroll(){
      var scrollElement = this.$el.getElementsByClassName('bby-f-dropdown')[0]
      var scrollHeight = scrollElement.scrollHeight
      var currentScrollPosition = scrollElement.scrollTop
      var scrollRatio = (currentScrollPosition / scrollHeight) * 100

      if (scrollRatio.toFixed(0) > 80){
        this.loadMore()
      }

    },
    loadMore(){
      this.fontEndPos = this.fontEndPos + 15
      if (!this.searchFontValue){
        this.googleFilteredFonts = this.getFontsSubset(this.googleFontsState)
      }
    }
  }
};
</script>

